import React from 'react'
import PropTypes from 'prop-types'
import Grid from '@material-ui/core/Grid'

import CommmingPng from './comming.png'
import Modal from '../modal'
import Text from '../../core/translations'

const CommingSoonModal = (props) => {
  const modalContent = {
    title: <Text text='commingSoon.title' />,
    content: <Grid container alignItems='center' justify='center'>
      <Grid item>
        <img
          alt='comming soon'
          style={{
          maxWidth: '94%',
          marginBottom: 12
        }}
          src={CommmingPng}
        />
      </Grid>
      <Grid item>
        <Text text='commingSoon.message' />
      </Grid>
    </Grid>,
    buttons: [
      {
        title: <Text text='commingSoon.back' />,
        color: 'green',
        onClick: props.onClose
      }
    ]
  }
  return <Modal
    isShowing={props.open}
    onClose={props.onClose}
    content={modalContent}
    maxWidth='xs'
  // fullScreen: PropTypes.bool,
  // width: PropTypes.number
  />
}

CommingSoonModal.propTypes = {
  onClose: PropTypes.func,
  open: PropTypes.bool
}

export default CommingSoonModal
