import React, { useState } from 'react'
import PropTypes from 'prop-types'

import { withRouter } from 'react-router'
import { UseAuthContext } from '../../context/auth/auth-context'
import { makeStyles } from '@material-ui/core/styles'
import Text from './../../core/translations'

import Background from '../background'
import Paper from '@material-ui/core/Paper'
import ProfileAvatar from '../avatar'

import Typography from '@material-ui/core/Typography'
import Modal from '../modal'

import { StyledLink } from './styled'

const useStyles = makeStyles(theme => ({
  paper: {
    width: '100%',
    padding: theme.spacing(3, 2)
  },
  content: {
    textAlign: 'center',
    lineHeight: '26px',
    display: 'inline-block',
    color: `${theme.palette.primary.black} !important`
  },
  name: {
    marginTop: theme.spacing(),
    color: `${theme.palette.primary.black} !important`
  },
  emoji: {
    marginTop: theme.spacing(2),
    fontSize: 32,
    display: 'block'
  }
}))

const Profile = ({ history }) => {
  const { name, image, logout } = UseAuthContext()
  const [ openLogoutDialog, setLogoutDialog ] = useState(false)
  const classes = useStyles()

  const logoutDialog = {
    title: <Text text='auth.logoutQuestion' />,
    content: <Typography className={classes.content}>
      <Text text='auth.logoutWarning' />
      <br />
      <span role='img' aria-label='emoji' className={classes.emoji}>😰</span></Typography>,
    buttons: [
      {
        title: <Text text='auth.cancel' />,
        color: 'white',
        textColor: '#F4424F',
        onClick: () => { setLogoutDialog(false) }
      },
      {
        title: <Text text='auth.logout' />,
        color: 'red',
        onClick: () => {
          logout()
          setLogoutDialog(false)
          history.push('/')
        }
      }]
  }

  return (
    <Background align='flex-start'>
      <Paper className={classes.paper}>
        <ProfileAvatar image={image} size='54' />
        <Typography className={classes.name} onClick={logout} variant='h5' gutterBottom align='center'>
          {name}
        </Typography>
        <Typography onClick={() => setLogoutDialog(true)} variant='body1' gutterBottom align='center'>
          <StyledLink to='#'><Text text='auth.logout' /></StyledLink>
        </Typography>

        <Modal
          onClose={() => { setLogoutDialog(false) }}
          isShowing={openLogoutDialog}
          content={logoutDialog}
          maxWidth='xl'
          />

      </Paper>
    </Background>
  )
}

Profile.propTypes = {
  history: PropTypes.object.isRequired
}

export default withRouter(Profile)
