import React from 'react'
import useGetScreenOrientation from '../../hooks/useScreenOrientation'

import styled from 'styled-components'
import Typography from '@material-ui/core/Typography'

import { Icons } from '../../helpers/icons'
import { Rotate } from '../background/animations'

const Backdrop = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  height: 100%;
  min-width: 100vw;
  min-height: 100vh;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  opacity: 1;
  transition: opacity 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  touch-action: none;
  background-color:rgba(0, 0, 0, 0.5);
  -webkit-tap-highlight-color: transparent;
`

const Wrapper = styled.div`
  position: absolute;
  width: 300px;
  height: 250px;
  text-align: center;
  border-radius: 10px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
`

const SVG = styled.div`
  animation-name: ${Rotate}; 
  animation-duration: 2s; 
  animation-iteration-count: infinite;
  transition-timing-function: ease-in-out;
`

const MobileOrientationCheck = () => {
  const { screenOrientation } = useGetScreenOrientation()

  return (
    screenOrientation === 'landscape'
      ? <Backdrop>
        <Wrapper>
          <SVG>
            {Icons.deviceRotate}
          </SVG>
          <Typography variant='body1'>
              We ondersteunen alleen <b>portrait</b> modus. Draai je scherm om verder te spelen.
          </Typography>
        </Wrapper>
      </Backdrop>
      : null
  )
}

export default MobileOrientationCheck
