import { useState, useEffect } from 'react'
import { useShootsContext } from '../context/shoots-context'

const useAudio = (url) => {
  const [audio] = useState(new window.Audio(url))
  const [playing, setPlaying] = useState()
  const { settings } = useShootsContext()

  useEffect(() => {
    if (settings) {
      setPlaying(settings.music)
    }
  }, [settings])

  // TODO maybe write this back to localstorage as well..
  // const toggle = () => setPlaying(!playing)

  useEffect(
    () => {
      playing
        ? (!audio.playing && audio.play())
        : audio.pause()
    },
    [playing]
  )

  return { playing }
}

export default useAudio
