import React, { memo } from 'react'
import PropTypes from 'prop-types'

import Typography from '@material-ui/core/Typography'

import { Icons } from './../../../helpers/icons'
import { isDebug } from './../../../helpers/shoots-helper'

import {
  Wrapper,
  CaptionBar,
  Caption,
  BigPlayerImage
} from './..//styled'

const Winner = ({ title, winner }) => {
  const DEBUG_MODE = isDebug()

  let winnerOfGame = winner

  if (DEBUG_MODE) {
    winnerOfGame = {
      name: 'Harige Bassie',
      image: 'https://api.adorable.io/avatars/60/Minatory-Sea-barnacles.png'
    }
  }

  return (
    <>
      <Wrapper>
        <Typography variant='h6' gutterBottom align='center'>
          { title }
        </Typography>

        {Icons.crown}
        <BigPlayerImage src={winnerOfGame && winnerOfGame.image} />
      </Wrapper>

      <CaptionBar>
        <Caption variant='h6'>
          {winnerOfGame && winnerOfGame.name} hoeft niet te drinken!
        </Caption>
      </CaptionBar>
    </>
  )
}

Winner.propTypes = {
  title: PropTypes.string,
  winner: PropTypes.object
}

export default memo(Winner)
