export const getPlayersTopThree = (players, responses, currentQuestion, winner) => {
  const wrongPlayers = players.filter(player => {
    return getPlayerResponse(player, responses).answer !== currentQuestion.correct
  }).sort((playerA, playerB) => getPlayerResponse(playerA, responses).time - getPlayerResponse(playerB, responses).time)

  if (wrongPlayers.length < 3 && wrongPlayers.length < players.length) {
    let count = 0
    const goodPlayersSorted = players.filter(player => {
      return getPlayerResponse(player, responses).answer === currentQuestion.correct
    })
      .sort((playerA, playerB) => getPlayerResponse(playerB, responses).time - getPlayerResponse(playerA, responses).time)

    while (wrongPlayers.length < players.length) {
      wrongPlayers.push(goodPlayersSorted[count])
      count++
    }
  }

  const result = wrongPlayers.slice(0, 3).filter(p => p.id !== winner.id)

  // fallback voor demo basically
  return result.length > 0 ? result : players
}

export const getFormattedQuestion = (gameState, shootsGame) => {
  const question = shootsGame.getCurrentQuestion.title
  const replacedPlayerName = question.replace('{PLAYER_NAME}', shootsGame.currentGameMaster.name)
  return replacePlayerPlaceholdersFromTitle(replacedPlayerName, gameState, shootsGame)
}

// dit gebruik ik om elke keer andere 4 players te gebruiken in de antwoorden.
// seed is dan bij iedereen hetzelfde, hash van de vraagnummer + round
function random (seed) {
  let x = Math.sin(seed) * 10000
  return x - Math.floor(x)
}

// shuffle met seed
function shuffle (a, seed) {
  let j, x, i
  let s = seed
  for (i = a.length - 1; i > 0; i--) {
    j = Math.floor(random(s++) * (i + 1))
    x = a[i]
    a[i] = a[j]
    a[j] = x
  }
  return a
}

const replacePlayerPlaceholders = (answer, playersShuffled) => {
  if (answer.includes('{PLAYER_')) {
    const index = ~~answer.substring(answer.indexOf('{PLAYER_') + 8)[0] - 1
    const player = playersShuffled[index]
    if (!player) {
      return null
    } else {
      return answer.replace(`{PLAYER_${index + 1}}`, player.name)
    }
  } else {
    return answer
  }
}

const replacePlayerPlaceholdersFromTitle = (title, gameState, shootsGame) => {
  const seed = shootsGame.currentRound + shootsGame.currentQuestion
  const playersShuffled = shuffle(gameState.players, seed).filter(player => player.id !== shootsGame.currentGameMaster.id)
  return replacePlayerPlaceholders(title, playersShuffled)
}

export const getFormattedAnswers = (gameState, shootsGame) => {
  const answers = shootsGame.getCurrentQuestion.answers
  const seed = shootsGame.currentRound + shootsGame.currentQuestion
  const playersShuffled = shuffle(gameState.players, seed).filter(player => player.id !== shootsGame.currentGameMaster.id)

  return answers.map(answer => {
    return replacePlayerPlaceholders(answer, playersShuffled)
  }).filter(answer => answer)
}

export const sortPlayersOnMostDrinks = (players, shootsGame) => {
  const result = {}
  shootsGame.drinkHistory && shootsGame.drinkHistory.forEach(drinks => {
    Object.keys(drinks).forEach(key => {
      if (!result[key]) {
        result[key] = 0
      }
      result[key] += drinks[key]
    })
  })

  return players.sort((a, b) => result[a.id] - result[b.id])
}

export const sortPlayersOnWin = (players, shootsGame) => {
  const historyWithSortedResponses = []
  shootsGame.responseHistory.forEach(responseObject => {
    const responsesSorted = responseObject.responses.sort((responseA, responseB) => {
      if (responseA.answer !== responseB.answer) {
        if (responseA.answer === responseObject.correct) {
          return -1
        } else {
          return 1
        }
      }
      return responseA.time - responseB.time
    })
    historyWithSortedResponses.push(responsesSorted)
  })

  const res = {}

  historyWithSortedResponses.forEach((responses) => {
    responses.forEach((response, i) => {
      res[response.playerId] = (res[response.playerId] || 0) + responses.length - i
    })
  })

  return players.sort((a, b) => res[b.id] - res[a.id])
}

export const isDebug = () => {
  const settings = JSON.parse(window.localStorage.getItem('settings'))

  if (settings && settings.debug) {
    return true
  } else {
    return window.location.href.includes('debug=true')
  }
}
