import React from 'react'
import PropTypes from 'prop-types'

import ProfilePicture from '../../context/auth/consumers/profile-picture'

import { withStyles } from '@material-ui/core/styles'
import styled from 'styled-components'
import { darken } from 'polished'

import Typography from '@material-ui/core/Typography'
import ButtonM from '@material-ui/core/Button'

import { LOGIN, CORE } from './../colors'

const styles = theme => ({
  margin: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1)
  },
  text: {
    width: '58%'
  }
})

const DEFAULTCOLOR = '#ffffff'
const DISABLEDCOLOR = '#E0E0E0'
const DISABLEDTEXT = '#A6A6A6'
const DISABLEDHOLDER = '#d0d0d0'
const DISABLEDLETTER = '#afafaf'
const DEFAULTHOLDER = '#00000036'

const StyledButton = styled.div`
  position: relative;
  overflow: hidden;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: ${props => !props.disabled ? 'pointer' : 'not-allowed'};
  width: 100%;
  min-height: 54px;
  font-size: 1em;
  font-weight: Bold;
  font-family: 'Lato', sans-serif;
  text-transform: ${props => props.uppercase ? 'uppercase' : 'capitalize'};
  text-align: center;
  text-shadow: 0 2px 20px rgba(0, 0, 0, 0.16);
  line-height: 30px;
  letter-spacing: 1px;
  color: white;
  background: #5a99d4;
  cursor: pointer;
  background-color: ${props => props.disabled ? DISABLEDCOLOR : props.backgroundcolor};
  color: ${props => props.disabled ? DISABLEDTEXT : props.textcolor}!important;
  border-color: ${props => props.disabled ? DISABLEDHOLDER : darken(0.1, props.backgroundcolor)};
  border-width: 3px;
  border-style: solid;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.20);
  border-radius: 200px;
  box-shadow: 0 2px 4px 0 rgba(14, 30, 37, 0.12);

  transition: all 0.2s cubic-bezier(.17,.67,1,1.23);
  transform: scale(1);

  max-width: calc(100vw - 64px);

  /** Left to Right **/
  /* background: ${props => `linear-gradient(to right, ${darken(0.05, props.backgroundcolor)} 50%, ${props.backgroundcolor} 50%)`};
  background-size: 200% 100%;
  background-position: right bottom;
  transition: all .2s ease-out;

  &:hover, &:focus {
    background-position: left bottom;
  } */

  /** Bottom to Top **/
  background-image: ${props => props.disabled
    ? 'none'
    : `linear-gradient(${darken(0.05, props.backgroundcolor)} 50%, ${props.backgroundcolor} 50%)`};
  background-size: auto 200%;
  background-position: 0 -100%;

  &:hover, &:focus {
    background-position: 0 0;
    transform: scale(1.02);
    outline: medium none;
  }
`

const getColor = (color, disabled) => {
  return {
    'facebook': LOGIN.FACEBOOK,
    'google': LOGIN.GOOGLE,
    'guest': LOGIN.GUEST,
    'blue': CORE.BLUE,
    'orange': CORE.ORANGE,
    'green': CORE.GREEN,
    'pink': CORE.PINK,
    'purple': CORE.PURPLE,
    'red': CORE.RED,
    'transparant': CORE.TRANSPARANT
  }[color]
}

const StyledLeftHolder = styled.div`
  width: 58px;
  height: 100%;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  padding-left: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: ${props => props.disabled ? DISABLEDHOLDER : DEFAULTHOLDER};
`
const StyledLetter = styled.span`
  font-size: 19px;
  display: block;
  font-weight: bold;
  color: ${props => props.disabled ? DISABLEDLETTER : DEFAULTCOLOR};
  text-transform: ${props => props.uppercase ? 'uppercase' : 'normal'};
`

const StyledIcon = styled.div`
  width: 40px;
  height: 40px;
  position: absolute;
  right: 8px;
  margin-right: ${props => props.position === 'left' ? '14px' : '0'};
  margin-left: ${props => props.position === 'right' ? ' 14px' : '0'};
  svg {
    vertical-align: inherit; //don't align in the 'middle'
  }
`

const FullWidthButton = styled(ButtonM)`
  width: 100%;
  height: 55px;
  color: ${props => props.textcolor} !important;
  background-color: ${props => props.disabled ? DISABLEDCOLOR : `${props.backgroundcolor} !important`};
  border-radius: 0px !important;
  margin-left: 0px !important;
  border-top: ${props => props.bordertop ? `1px solid ${DISABLEDCOLOR}` : 'none'}!important;
`
const FlatButton = styled(ButtonM)`
  height: 55px;
  color: ${props => props.textcolor} !important;
  background-color: ${DISABLEDCOLOR};
  border-radius: 0px !important;
  margin-left: 0px !important;
  font-size: ${props => props.fontSize ? `${props.fontSize}px!important` : `19px!important`};
  border-top: none;
`

const getIcon = (icon) => ({
  'facebook': <svg aria-label='Facebook' viewBox='0 0 512 512' preserveAspectRatio='xMidYMid meet'>
    <path fill={DEFAULTCOLOR} d='M211.9 197.4h-36.7v59.9h36.7V433.1h70.5V256.5h49.2l5.2-59.1h-54.4c0 0 0-22.1 0-33.7 0-13.9 2.8-19.5 16.3-19.5 10.9 0 38.2 0 38.2 0V82.9c0 0-40.2 0-48.8 0 -52.5 0-76.1 23.1-76.1 67.3C211.9 188.8 211.9 197.4 211.9 197.4z' />
  </svg>,
  'google': <svg aria-label='Google+' role='img' viewBox='0 0 512 512'>
    <path fill={DEFAULTCOLOR} d='M191 234v47h72c-2 19-21 55-72 55-43 0-78-36-78-80s33-80 76-80c25 0 42 10 51 19l35-33a125 125 0 0 0-211 94c0 70 57 127 127 127 74 0 122-53 122-125l-2-24' />
    <path stroke={DEFAULTCOLOR} strokeWidth='29' d='M404 192v118m59-59H345' />
  </svg>
})[icon]

const Button = (props) => {
  const {
    classes,
    onClick,
    title,
    icon,
    iconPosition,
    background,
    textColor,
    fontSize,
    upperCase,
    disabled,
    variant,
    letter,
    borderTop
  } = props

  const position = iconPosition.toLowerCase()
  const backgroundColor = background.toLowerCase()

  const Icon = icon !== null
    ? <StyledIcon position={position}>
      {
        typeof (icon) === 'string' && icon.indexOf('http') === -1 && icon.indexOf('data:') === -1
          ? getIcon(icon.toLowerCase())
          : <ProfilePicture image={icon} />
      }
    </StyledIcon>
    : null

  if (variant === 'icon') {
    return (
      <StyledButton
        className={classes.margin}
        backgroundcolor={getColor(backgroundColor, disabled)}
        textcolor={textColor}
        uppercase={upperCase}
        disabled={disabled}
        bordertop={borderTop}
        onClick={
          disabled
            ? () => {}
            : typeof (onClick) === 'function'
              ? (e) => onClick(e)
              : () => console.error('no onClick specified')
          }
        >
        { position === 'left' && Icon }
        <span className={classes.text}>{title}</span>
        { position === 'right' && Icon }
      </StyledButton>
    )
  }

  if (variant === 'correct') {
    return (
      <StyledButton
        className={classes.margin}
        backgroundcolor={getColor('green')}
        textcolor={textColor}
        uppercase={upperCase}
        bordertop={borderTop}
        >
        <StyledLeftHolder>
          <StyledLetter>
            <svg fill={DEFAULTCOLOR} width='24' height='24' viewBox='0 0 24 24'><path d='M9 21.035l-9-8.638 2.791-2.87 6.156 5.874 12.21-12.436 2.843 2.817z' /></svg>
          </StyledLetter>
        </StyledLeftHolder>

        <Typography variant='h6' className={classes.text}>
          {title}
        </Typography>
      </StyledButton>
    )
  }

  if (variant === 'wrong') {
    return (
      <StyledButton
        className={classes.margin}
        backgroundcolor={getColor('red')}
        textcolor={textColor}
        uppercase={upperCase}
        bordertop={borderTop}
        >
        <StyledLeftHolder>
          <StyledLetter>
            {letter}
          </StyledLetter>
        </StyledLeftHolder>

        <Typography variant='h6' className={classes.text}>
          {title}
        </Typography>
      </StyledButton>
    )
  }

  if (variant === 'question') {
    return (
      <StyledButton
        className={classes.margin}
        backgroundcolor={getColor(backgroundColor)}
        textcolor={textColor}
        uppercase={upperCase}
        disabled={disabled}
        bordertop={borderTop}
        onClick={
            disabled
              ? () => {}
              : typeof (onClick) === 'function'
                ? (e) => onClick(e)
                : () => console.error('no onClick specified')
            }
        >
        <StyledLeftHolder disabled={disabled}>
          <StyledLetter disabled={disabled}>{letter}</StyledLetter>
        </StyledLeftHolder>

        <Typography variant='h6' className={classes.text}>
          <StyledLetter disabled={disabled}>{title}</StyledLetter>
        </Typography>

      </StyledButton>
    )
  }

  if (variant === 'fullWidth') {
    const color = getColor(backgroundColor)

    return (
      <FullWidthButton
        size='small'
        textcolor={textColor}
        backgroundcolor={color}
        onClick={onClick}
        disabled={disabled}
        bordertop={borderTop}
        >
        {title}
      </FullWidthButton>
    )
  }

  if (variant === 'flat') {
    const color = getColor(backgroundColor)

    return (
      <FlatButton
        textcolor={textColor}
        backgroundcolor={color}
        onClick={onClick}
        disabled={disabled}
        bordertop={borderTop}
        fontSize={fontSize}
        >
        {title}
      </FlatButton>
    )
  }
}

Button.defaultProps = {
  disabled: false,
  upperCase: false,
  textColor: DEFAULTCOLOR,
  iconPosition: 'left',
  icon: 'null',
  variant: 'icon',
  background: 'green',
  borderTop: 'false'
}

Button.propTypes = {
  classes: PropTypes.object.isRequired,
  onClick: PropTypes.func.isRequired,
  title: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  background: PropTypes.string,
  textColor: PropTypes.string.isRequired,
  fontSize: PropTypes.number,
  icon: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.string
  ]),
  iconPosition: PropTypes.string,
  disabled: PropTypes.bool,
  upperCase: PropTypes.bool,
  borderTop: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.bool
  ]),
  correct: PropTypes.string,
  variant: PropTypes.oneOf([
    'icon',
    'question',
    'flat',
    'correct',
    'wrong',
    'disabled',
    'fullWidth'
  ])
}

export default withStyles(styles)(Button)
