import React, { useState, useRef, useEffect } from 'react'
import PropTypes from 'prop-types'

import { UseAuthContext } from './auth'
import { withRouter } from 'react-router-dom'

import socketIOClient from 'socket.io-client'

import * as actions from './actions'
import * as gameActions from './game-actions/actions'

import get from 'lodash/get'
import ReactNotification from 'react-notifications-component'

import 'react-notifications-component/dist/theme.css'
import apiUrl from './api'

import getIP from '../helpers/get-ip'
import { shootsGame as devShootsGame, gameState as devGameState } from './fake-dev-state'
import { isDebug, sortPlayersOnMostDrinks } from '../helpers/shoots-helper'

const ShootsContext = React.createContext({})

const useShootsContext = () => {
  const context = React.useContext(ShootsContext)
  if (!context) {
    throw new Error('No context found for useShootsContext')
  }

  return context
}

const socket = socketIOClient(apiUrl)

const ShootsProviderContext = ({ children, history }) => {
  const auth = UseAuthContext()
  const notificationRef = useRef()

  const [ip, setIp] = useState(window.localStorage.getItem('ip'))

  const [lang, _setLang] = useState(window.localStorage.getItem('_lang') || 'en')
  const [settings, _setSettings] = useState(JSON.parse(window.localStorage.getItem('settings')))

  const [gameState, setGameState] = useState(isDebug() ? devGameState : {})
  const [shootsGame, setShootsGame] = useState(isDebug() ? devShootsGame : {})

  const setLang = (language) => {
    window.localStorage.setItem('_lang', language)
    _setLang(language)
  }

  const setSettings = (settings) => {
    window.localStorage.setItem('settings', JSON.stringify(settings))
    _setSettings(settings)
  }

  useEffect(() => {
  // MAIN STATE
    socket.on('set-game-state', (res) => {
      console.log('setting game state...', res)
      setGameState((prev) => ({ ...prev, ...res }))
    })

    // game specific state
    socket.on('set-gameclass-state', (res) => {
      console.log('setting shoots game state...', res)
      setShootsGame((prev) => ({ ...prev, ...res }))
    })

    socket.on('notification', (res) => {
      pushNotification(res)
    })

    socket.on('created-game', (res) => {
      console.log('context created game', res)

      setGameState(prevState => {
        return { ...prevState, ...res }
      })
    })

    socket.on('game-destroyed', mofo => {
      console.log('1')
      pushNotification({
        title: 'Fuck!',
        message: `${mofo} destroyed the game!`,
        type: 'danger'
      })
      history.push('/')
    })

    socket.on('error-code', res => {
      if (res.status === 402) {
        auth.logout()
      }
    })

    socket.on('player-joined-game', (res) => {
      console.log('PLAYER JOINED', res)
      setGameState(prevState => {
        return { ...prevState, ...res }
      })
      history.push('/waiting')
    })

    socket.on('error', (res) => {
      console.log('ERORROROROOR', res)
      console.error(res.message)
    })

    socket.on('rematch-game', res => {
      setGameState(prevState => {
        return { ...prevState, ...res }
      })
      setShootsGame({})
      history.push('/waiting')
    })

    socket.on('local-games', localGames => {
      setGameState(prevState => {
        return { ...prevState, localGames }
      })
    })

    socket.on('local-games', localGames => {
      setGameState(prevState => {
        return { ...prevState, localGames }
      })
    })

    socket.on('game-overview', (data) => {
      const playersOnMostDrinks = sortPlayersOnMostDrinks()

      if (playersOnMostDrinks[0].id === gameState.playerId) {
        history.push('/most-drinks')
      } else {
        history.push('/game-overview')
      }
    })
  }, [])

  const pushNotification = (res) => {
    notificationRef.current.addNotification({
      title: res.title,
      message: res.message,
      type: res.type,
      insert: res.insert || 'top',
      container: res.container || 'top-right',
      animationIn: ['animated', 'fadeIn'],
      animationOut: ['animated', 'fadeOut'],
      dismiss: { duration: res.duration || 2000 },
      dismissable: { click: true }
    })
  }

  const isGameMaster = () => {
    return get(shootsGame, 'currentGameMaster.id') === get(gameState, 'playerId')
  }

  const destroyGame = () => {
    setGameState({})
    setShootsGame({})
  }

  const everyOneHadSameMistake = () => {
    return false
    // uit gezet omdat johnny het niet snapte
    // let prev = -1
    // let res = true
    // shootsGame.responses.forEach(response => {
    //   if (prev === -1) {
    //     prev = response.answer
    //   }
    //   if (response.answer === shootsGame.getCurrentQuestion.correct || response.answer !== prev) {
    //     res = false
    //     return res
    //   }
    // })

    // return res
  }

  const getWinner = () => {
    let quickestTime = { time: Infinity, playerId: null }

    // even uit gezet omdat johnny het niet snapte
    // const everyOneWrong = everyOneHadSameMistake()
    const everyOneWrong = false

    shootsGame.responses.forEach(response => {
      if (quickestTime.time > response.time && (response.answer === shootsGame.getCurrentQuestion.correct || everyOneWrong)) {
        quickestTime = response
      }
    })

    if (quickestTime.playerId) {
      return gameState.players.find(player => player.id === quickestTime.playerId)
    } else {
      const gameMaster = gameState.players.find(player => player.id === shootsGame.currentGameMaster.id)
      return gameMaster
    }
  }

  const bindedActions = []
  Object.entries(actions).forEach(entry => {
    const { type } = entry[1]()
    bindedActions[entry[0]] = data => {
      console.log(type, auth, data)
      socket.emit(type, { auth, data })
    }
  })

  const bindedGameActions = []
  Object.entries(gameActions).forEach(entry => {
    const { type } = entry[1]()
    bindedGameActions[entry[0]] = data => socket.emit('update-game-status', { auth, data, type })
  })

  console.log('Game State -----', gameState)
  console.log('Shoots State -----', shootsGame)

  useEffect(() => {
    getIP((data) => {
      if (!window.localStorage.getItem('ip')) {
        window.localStorage.setItem('ip', data.ip)
        setIp(data.ip)
      }

      if (!window.localStorage.getItem('customLang')) {
        const language = data.languages.substring(0, data.languages.indexOf('-'))
        setLang(language)
      }
    })
  }, [])

  return (
    <ShootsContext.Provider
      value={{
        isHost: gameState.hostId && (gameState.hostId === gameState.playerId),
        setGameState,
        gameState,
        shootsGame,
        actions: bindedActions,
        gameActions: bindedGameActions,
        isGameMaster: isGameMaster,
        getWinner: getWinner,
        destroyGame: destroyGame,
        pushNotification: pushNotification,
        isWinner: () => {
          const winner = getWinner()
          return winner ? (winner.id === gameState.playerId) : {}
        },
        isSpectator: gameState.players && !gameState.players.some(player => player.id === gameState.playerId),
        ip: ip,
        everyOneHadSameMistake: everyOneHadSameMistake,
        lang: lang,
        setLang: setLang,
        setSettings: setSettings,
        settings: settings
      }}
      >
      <ReactNotification ref={notificationRef} />
      {children}
    </ShootsContext.Provider>
  )
}

ShootsProviderContext.propTypes = {
  children: PropTypes.any.isRequired,
  history: PropTypes.object.isRequired
}

export { useShootsContext, ShootsContext }
export default withRouter(ShootsProviderContext)
