import React, { memo, useState } from 'react'
import PropTypes from 'prop-types'

import Button from '../../core/button'
import { withStyles } from '@material-ui/core'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'
import Timer from '../status/wait-timer'
import Styled from 'styled-components'

import { isDebug } from '../../helpers/shoots-helper'

const ButtonColors = ['purple', 'blue', 'orange', 'pink']
const Alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'

const QUESTION = 'question'
const CORRECT = 'correct'
const WRONG = 'wrong'
const DISABLED = 'disabled'

const styles = theme => ({
  emoji: {
    fontSize: 40
  },
  intro: {
    textTransform: 'uppercase'
  }
})

const StyledCard = Styled(Card)`
  display: flex;
  justify-content: flex-start;
  padding: 20px;
  align-items: center;
  flex-direction: column;
  height: 87vh;
`

const StyledCardContent = Styled(CardContent)`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  padding: 0 !important;
  padding-top: 16px !important;
`

const Question = Styled.p`
  font-size: 21px;
  display: block;
  font-weight: bold;
  color: #242970;
  padding-top: 44px;
`

const AnswerWrapper = Styled.div`
  margin-top: 20px;
  width: 100%;
  overflow: hidden;
`

const Quiz = ({ classes, time, question, answers, correct, onFinish, currentPart, totalParts, onSelect, percentages }) => {
  const [selectedAnswer, setSelectedAnswer] = useState(null)
  const [stepFinshed, setStepFinished] = useState(false)
  const [timer, setTimer] = useState(0)

  const DEBUG_MODE = isDebug()

  const handleButtonClick = index => {
    setSelectedAnswer(index)
    setStepFinished(true)

    setTimeout(() => {
      const answerTime = time - timer.time * 1000
      if (!stepFinshed && !DEBUG_MODE) {
        onSelect(index, answerTime)
      }
    }, 1500)
  }

  const getButtonCollor = index => {
    let i = index
    while (i > ButtonColors.length - 1) {
      i -= ButtonColors.length
    }
    return ButtonColors[i]
  }

  const getButtonStatus = index => {
    if (stepFinshed) {
      if (selectedAnswer === null) {
        return DISABLED
      }
      if (correct === index || (correct === null && selectedAnswer === index)) {
        return CORRECT
      } else if (correct !== selectedAnswer && selectedAnswer === index) {
        return WRONG
      } else {
        return DISABLED
      }
    }
    // not finished yet
    if (selectedAnswer !== null && selectedAnswer !== index) {
      return DISABLED
    } else {
      return QUESTION
    }
  }

  const timerFinish = () => {
    if (!DEBUG_MODE) {
      if (selectedAnswer !== null) {
        onSelect(selectedAnswer, time - timer.time * 1000)
      }
      onFinish()
    }
  }

  const getPercentage = (answer) => {
    const count = percentages.filter(r => r.answer === answer).length
    return Math.round((100 / percentages.length) * count)
  }

  return <StyledCard>
    <StyledCardContent>
      <Timer
        stop={selectedAnswer !== null || percentages}
        setTimer={setTimer}
        zeroText='Done'
        time={time / 1000}
        onFinish={timerFinish}
        size={80}
      />
      {
        currentPart && (
        <Typography gutterBottom className='pt-2' variant='subtitle1'>
            {currentPart + 1} / {totalParts}
          </Typography>
        )}
      <Question className='mb-4'>
        {question}
      </Question>
    </StyledCardContent>
    <AnswerWrapper>
      {
          answers.map((answer, i) => {
              const status = getButtonStatus(i)
              return <div style={{ position: 'relative' }}>
                <Button
                  title={answer}
                  letter={Alphabet[i]}
                  key={i}
                  disabled={status === DISABLED}
                  variant={status === DISABLED ? QUESTION : status}
                  background={getButtonCollor(i)}
                  onClick={() => handleButtonClick(i)}
                />
                {
                  percentages && <span style={{
                    position: 'absolute',
                    right: '23px',
                    top: '33%',
                    color: 'white',
                    fontWeight: 'bolder'
                  }}>{getPercentage(i)}%</span>
                }
              </div>
          })
        }
    </AnswerWrapper>
  </StyledCard>
}

Quiz.propTypes = {
  classes: PropTypes.object.isRequired,
  time: PropTypes.number.isRequired,
  question: PropTypes.string,
  answers: PropTypes.array,
  correct: PropTypes.number,
  onFinish: PropTypes.func,
  currentPart: PropTypes.number,
  totalParts: PropTypes.number,
  onSelect: PropTypes.func,
  percentages: PropTypes.array
}

Quiz.defaultProps = {
  correct: null
}

export default memo(withStyles(styles)(Quiz))
