import React, { useState, useEffect } from 'react'
import ReactDOM from 'react-dom'

import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles'
import styled from 'styled-components'

import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
// import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'

import Slide from '@material-ui/core/Slide'

// import IconButton from '@material-ui/core/IconButton'
// import CloseIcon from '@material-ui/icons/Close'

import Button from '../../core/button'

const useStyles = makeStyles(theme => ({
  dialogPaper: ({ width }) => ({
    width: width
  }),
  // fucks up animation
  // root: {
  //   margin: 0,
  //   textAlign: 'center',
  //   'overflow-y': 'auto'
  // },
  title: {
    textAlign: 'center',
    marginBottom: 0,
    paddingBottom: `0px !important`
  },
  header: {
    fontSize: 19,
    fontWeight: 600,
    color: `${theme.palette.primary.black} !important`
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500]
  },
  text: {
    textAlign: 'center',
    fontSize: 24,
    color: theme.palette.primary.dark + '!important'
  },
  content: {
    textAlign: 'center',
    padding: theme.spacing(3),
    lineHeight: '26px',
    'overflow-y': 'auto',
    color: `${theme.palette.primary.dark} !important`
  },
  custom: {
    textAlign: 'center',
    lineHeight: '26px',
    'overflow-y': 'hidden',
    color: `${theme.palette.primary.grey} !important`
  },
  actions: {
    margin: `0px !important`,
    padding: `0px !important`
  }
}))

const IllustrationHolder = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
  overflow: hidden;
`

const Illustration = styled.img`
  width: 160px;
  height: 160px;
  border: none;
  outline: none;
  object-fit: cover;
`

const Transition = React.forwardRef(function Transition (props, ref) {
  return <Slide direction='up' ref={ref} {...props} />
})
const Modal = ({ customContent, width, isShowing, onClose, maxWidth, fullScreen, content: { title, content, img, buttons }, ...other }) => {
  const classes = useStyles({ width })
  const [mounted, setMount] = useState(false)

  useEffect(() => {
    setMount(true)
  }, [])

  // pas de portal renderen na de mount. data.style error style of undefined
  return (
    mounted && ReactDOM.createPortal(
      <div className='modal'>
        <Dialog
          disableBackdropClick
          disableEscapeKeyDown
          PaperProps={{ classes: { root: classes.dialogPaper } }}
          className={classes.root}
          onClose={onClose}
          aria-labelledby='dialog-title'
          TransitionComponent={Transition}
          keepMounted
          open={isShowing}
          maxWidth={maxWidth}
          fullScreen={fullScreen}
          {...other}
          >
          <DialogTitle className={classes.title}>
            {
              // onClose
              // ? (
              //   <React.Fragment>
              //     <IconButton aria-label='Close' className={classes.closeButton} onClick={onClose}>
              //       <CloseIcon />
              //     </IconButton>
              //     <Typography className={classes.header} variant='h6'>{ title }</Typography>
              //   </React.Fragment>
              // )
              <span className={classes.header}>{ title }</span>
              }
          </DialogTitle>
          <DialogContent className={classes.content}>
            {
                customContent
                  ? content
                  : <div className={classes.custom}>
                    {
                      img &&
                      <IllustrationHolder>
                        <Illustration src={img} alt='drinks' />
                      </IllustrationHolder>
                      }
                    { content }
                  </div>
              }
          </DialogContent>
          <DialogActions disableSpacing className={classes.actions}>
            {
               buttons.map(({ color, title, onClick, borderTop, textColor = '#ffffff' }, index) => <Button key={`button--${index}`} background={color} title={title} onClick={onClick} borderTop={borderTop} variant='fullWidth' textColor={textColor} />)
              }
          </DialogActions>
        </Dialog>
      </div>,
      document.getElementById('portal')
    )

  )
}

Modal.defaultProps = {
  maxWidth: 'md',
  customContent: false,
  fullScreen: false,
  isShowing: false
}

Modal.propTypes = {
  isShowing: PropTypes.bool,
  onClose: PropTypes.func.isRequired,
  content: PropTypes.object.isRequired,
  maxWidth: PropTypes.string,
  fullScreen: PropTypes.bool,
  width: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  customContent: PropTypes.bool
}

export default Modal
