import React, { memo } from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'

import { UseAuthContext } from '../../context/auth'

import { Grid, Typography } from '@material-ui/core'
import ProfilePicture from '../../context/auth/consumers/profile-picture'
import { Icons } from '../../helpers/icons'
import { useShootsContext } from '../../context/shoots-context'
import { isDebug } from '../../helpers/shoots-helper'
// import { howMuchPlayerDrink } from '../../helpers/shoots-helper'

const Golden = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F79D1B; // iets donker gold
`

const StyledDrinks = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px;
`

const formatName = name => name && name.length > 15 ? name.split(' ')[0] : name

const PlayerPosition = styled(Grid)`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items:center;
  padding: 16px;
  text-align: left;
  border-top: 1px solid #946DFF;
  background: ${({ highlight, theme }) => highlight ? '#a07eff' : 'none'};

  * {
    color: white
  }
`

const PlayersList = ({ waiting, players, responses, gameMaster, drinks }) => {
  const { authenticated, name } = UseAuthContext()
  const { gameState, shootsGame } = useShootsContext()

  let CURRENT_PLAYERS = players

  console.log(drinks)
  console.log('PLAYERS view', authenticated)
  console.log(gameMaster)

  const DEBUG_MODE = isDebug()
  if (DEBUG_MODE) {
    CURRENT_PLAYERS = gameState.players
  }

  const HOST = CURRENT_PLAYERS.find(p => p.id === gameState.hostId)

  const showIconOrPoints = (variant, player, isGameMaster) => {
    if (waiting) {
      if (isGameMaster) {
        return Icons.crown
      } else {
        return null
      }
    } else {
      const displayDinks = drinks || shootsGame.drinks

      return displayDinks && (
        <StyledDrinks>
          <Golden>
            <Typography variant={variant}>{displayDinks ? (displayDinks[player.id] || 0) : '-'}x</Typography>
          </Golden>
          {Icons.smallDrink}
        </StyledDrinks>
      )
    }
  }

  return (
    CURRENT_PLAYERS.map((player, i) => {
      const isHost = player.isHost
      const you = name === player.name
      const variant = you ? 'h6' : 'body2'

      const getResponseTime = (player) => {
        const response = responses && responses.find(response => response.playerId === player.id)
        return response && response.time
      }

      const responseTime = getResponseTime(player)

      return <PlayerPosition key={`player_${i}`} container highlight={you.toString()}>
        <Grid item xs={2}>
          <Typography className='pl-2' variant={variant}>{i + 1}.</Typography>
        </Grid>
        <Grid item xs={2}>
          <ProfilePicture style={{ marginLeft: -16 }} image={player.image} />
        </Grid>
        <Grid container item xs={4}>
          <Typography display='inline' variant='h6'>{formatName(player.name)}</Typography>
          {
            waiting && isHost
            ? <Typography style={{ width: '100%' }} display='inline' variant='subtitle1'>Game host</Typography>
            : <Typography variant='body2' style={{ width: '100%' }} display='inline'>
              {
                  HOST && (HOST.id === player.id)
                    ? 'Host'
                    : responseTime && `${Math.round(responseTime / 100) / 10}s`
                }
            </Typography>
          }
        </Grid>
        <Grid container justify='flex-end' item xs={4}>
          <Grid item>
            { showIconOrPoints(variant, player, waiting && isHost) }
          </Grid>
        </Grid>
      </PlayerPosition>
    })

  )
}

PlayersList.defaultProps = {
  waiting: false
}

PlayersList.propTypes = {
  waiting: PropTypes.bool,
  responses: PropTypes.array,
  drinks: PropTypes.object
}

export default memo(PlayersList)
