import React, { memo, useEffect, useState } from 'react'
import PropTypes from 'prop-types'

import TextField from '@material-ui/core/TextField'

import GameCard from '../game-card'
import { useShootsContext } from '../../context/shoots-context'

const SpectatorView = ({ history }) => {
  const { actions, gameState } = useShootsContext()
  const [pin, setPin] = useState('')

  useEffect(() => {
    if (history.location.search.indexOf('?id=') > -1) {
      actions.joinSpectator({ pin: history.location.search.replace('?id=', '') })
    }
  }, [])

  useEffect(() => {
    if (pin.length === 5) {
      actions.joinSpectator({ pin })
    }
  }, [pin])

  useEffect(() => {
    if (gameState.hostName) {
      history.push('/waiting')
    }
  })

  return <div>
    <p>Maak een nieuwe game aan op je mobiel en voer hier de pin in!</p>

    <TextField
      inputProps={{ maxLength: 5 }}
      InputLabelProps={{ shrink: false }}
      onChange={evt => setPin(evt.target.value)}
      value={pin}
      variant='outlined'
      margin='normal'
      label=''
      id='pin-control'
      />
    <div>
      {
        gameState.localGames && gameState.localGames.map((game) => {
          return <GameCard
            hostName={game.hostName}
            hostPicture={game.players.find(p => p.isHost).image}
            playerCount={game.players.length}
            onClick={() => actions.joinSpectator({ pin: game.pin })}
          />
        })
      }
    </div>
  </div>
}

SpectatorView.propTypes = {
  history: PropTypes.object.isRequired
}

export default memo(SpectatorView)
