import React, { memo, useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import useModal from '../../hooks/useModal'

import { useShootsContext } from '../../context/shoots-context'

import { withStyles } from '@material-ui/core/styles'
import styled from 'styled-components'

import { isDebug } from '../../helpers/shoots-helper'
import Button from '../../core/button'

import DrinksModal from './drinks-modal'
import Background from '../background'
import Winner from '../waiting/winner'
import PlayersList from '../players-list'

const styles = theme => ({
  header: {
    fontSize: 19,
    fontWeight: 600,
    color: `${theme.palette.primary.black} !important`
  }
})

const Wrapper = styled.div`
  position: absolute;
  bottom: 24px;
  left: 0;
  right: 0;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
`

const Scores = ({ history }) => {
  const DEBUG_MODE = isDebug()
  const { getWinner, gameState, shootsGame, gameActions, isWinner, isSpectator, everyOneHadSameMistake } = useShootsContext()
  const player = gameState && gameState.players && gameState.players.find(p => p.id === gameState.playerId)

  const everyOneWrong = everyOneHadSameMistake()

  const gameMaster = gameState && gameState.players && gameState.players.find(player => player.id === shootsGame.currentGameMaster.id)
  const winner = getWinner()

  const gameMasterHasWon = gameMaster.id === winner.id

  const { toggle, isShowing } = useModal(false)
  const [modalShown, setModalIsShown] = useState(false)

  useEffect(() => {
    // winner is altijd ready
    if (winner.id === gameState.playerId) {
      gameActions.readyForNextRound(player.id)
    }
  }, [])

  useEffect(() => {
    if (!DEBUG_MODE && !isWinner() && shootsGame.drinks && Object.keys(shootsGame.drinks).length > 0 && !modalShown && !isShowing && !isSpectator) {
      setModalIsShown(true)
      toggle()
    }
  })

  useEffect(() => {
    if (shootsGame.playersReady === gameState.players.length) {
      setTimeout(() => {
        if (shootsGame.gameFinished) {
          history.push('/game-end')
        } else {
          history.push('/countdown')
          if (player && player.isHost) {
            gameActions.nextQuestion()
          }
        }
      }, 500)
    }
  })

  const doneClick = () => {
    toggle()
    gameActions.readyForNextRound(player.id)
  }

  const playersExceptWinner = gameState.players.filter(p => p.id !== winner.id)
  const myDrinks = shootsGame && shootsGame.drinks && Object.keys(shootsGame.drinks).length > 0 ? (shootsGame.drinks[gameState.playerId] || 0) : 0

  return (
    <>
      <Background showCircles align='flex-start' noPadding>
        <DrinksModal open={isShowing} doneClick={doneClick} amountOfDrinks={myDrinks} />
        <Winner
          title={gameMasterHasWon ? 'De Gamemaster wint! Iedereen had het fout.' : everyOneWrong ? 'Iedereen had hetzelfde antwoord, behalve de Gamemaster!' : `${winner.name} had het goed en was het snelst!`}
          winner={winner}
        />
        <PlayersList
          pin={gameState.pin}
          players={playersExceptWinner}
          responses={shootsGame.responses}
          currentQuestion={shootsGame.getCurrentQuestion}
          gameMaster={gameMaster}
        />
        {
        DEBUG_MODE && <>
          <Wrapper>
            <Button title='Show Drinks Modal' background='red' onClick={toggle} />
          </Wrapper>
        </>
      }
      </Background>
    </>
  )
}

Scores.propTypes = {
  history: PropTypes.object.isRequired
}

export default memo(withStyles(styles)(Scores))
