import { useEffect, useState } from 'react'
import { MD } from '../helpers/mobile-detect'

const useGetScreenOrientation = () => {
  const [screenOrientation, setScreenOrientation] = useState(null)
  const onMobileDevice = MD(true)
  useEffect(() => {
    const orientation = window.screen.orientation && window.screen.orientation.angle
    console.log('Angle screen', orientation)

    const updateOrientation = () => {
      if ((typeof (window.matchMedia) !== 'undefined') && onMobileDevice) {
        if (window.matchMedia('(orientation: portrait)').matches) {
          if (document.body.classList.contains('message-open')) {
            document.body.classList.remove('message-open')
          }
          setScreenOrientation('portrait')
        }

        if (window.matchMedia('(orientation: landscape)').matches) {
          document.body.classList.add('message-open')
          setScreenOrientation('landscape')
        }
      }
    }

    window.addEventListener('resize', updateOrientation)
    updateOrientation()

    return () => window.removeEventListener('resize', updateOrientation)
  }, [])

  return { screenOrientation }
}

export default useGetScreenOrientation
