import React, { useEffect, useState, memo } from 'react'
import PropTypes from 'prop-types'
import { withRouter } from 'react-router'

import isEmpty from 'lodash/isEmpty'

import Grid from '@material-ui/core/Grid'
import CategoryCard from './card'
import Background from '../background'

import Typography from '@material-ui/core/Typography'
import styled from 'styled-components'
import { useShootsContext } from '../../context/shoots-context'
import { UseAuthContext } from '../../context/auth'

import Loader from '../../core/loader'

import apiUrl from '../../context/api'

const colors = ['#04e0a0', '#5897fa', '#f79d1b']

const DUMMY_USER = 'Bassie100'

const Container = styled.div`
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
  height: 100%;
`
const Header = styled.div`
  width: 100%;
  padding: ${({ theme }) => theme.spacing(4) + 'px'};
`

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
`

const Categories = ({ history }) => {
  const [categories, setCategories] = useState(window._CATEGORIES)
  const { name } = UseAuthContext()
  const { actions, ip } = useShootsContext()
  const [time, setTime] = useState(false)

  useEffect(() => {
    const setLoader = () => {
      setTime(true)
    }

    if (isEmpty(categories)) {
      window.fetch(apiUrl + '/api/categories')
        .then(res => res.json())
        .then(res => {
          window._CATEGORIES = res.data.data
          setCategories(res.data.data)
        })
        .catch(err => console.error(err))

      setTimeout(setLoader, 200)
      return clearTimeout(setLoader)
    }
  }, [])

  const handleCategoryClick = (identifier, categoryName) => {
    let idFromLocal = window.localStorage.getItem('player-id')
    if (!idFromLocal) {
      const randomKey = Math.random().toString(36).substring(7)
      window.localStorage.setItem('player-id', randomKey)
      idFromLocal = randomKey
    }

    const data = {
      hostName: name || DUMMY_USER,
      category: identifier,
      categoryName: categoryName,
      gameType: 'shoots',
      ip: ip,
      playerId: idFromLocal
    }

    actions.createGame(data)

    window.localStorage.removeItem('gamePin')

    history.push(`/waiting`)
  }

  return (
    <Background align='flex-start'>
      <Container>
        <Header>
          <Typography variant='h6' component='h2'>
            Kies een categorie
          </Typography>
        </Header>
        <Grid
          container
          direction='row'
          alignItems='center'
          spacing={2}
        >
          {
            !isEmpty(categories)
            ? categories.map(({ categoryId, icon, categoryName, subtitle }, i) =>
             (
               <Grid item xs={6} key={categoryId}>
                 <CategoryCard
                   id={categoryId}
                   icon={icon}
                   title={categoryName}
                   subtitle={subtitle}
                   color={colors[i]}
                   index={i}
                   onClick={() => handleCategoryClick(categoryId, categoryName)}
               />
               </Grid>
             ))
             : time
              ? <Wrapper><Loader text={'Categorieën laden..'} /></Wrapper>
              : <Wrapper><h4>If you see this, something is really broken.. :-(</h4></Wrapper>
          }
        </Grid>
      </Container>
    </Background>
  )
}

Categories.propTypes = {
  history: PropTypes.object.isRequired
}

export default memo(withRouter(Categories))
