const adjectives = ['coole', 'malle', 'dikke', 'domme', 'drop', 'whip', 'malse']
const nouns = ['kikker', 'gozer', 'makker', 'gans', 'peller', 'saus', 'cat']

function randomEl (list) {
  const i = Math.floor(Math.random() * list.length)
  return list[i]
}

const upper = (lower) => lower.replace(/^\w/, c => c.toUpperCase())

export default () => upper(randomEl(adjectives)) + ' ' + upper(randomEl(nouns))
