import React, { useEffect, useState } from 'react'

import PropTypes from 'prop-types'
import Background from '../background'
import QuizScreen from '../quiz/quiz-screen'
import { useShootsContext } from '../../context/shoots-context'
import { getFormattedQuestion, getFormattedAnswers } from '../../helpers/shoots-helper'

const SlaveQuiz = ({ history }) => {
  // not game master
  const { gameActions, gameState, shootsGame } = useShootsContext()
  const [answer, setAnswer] = useState(null)
  const [showPercentages, setShowPercentages] = useState(false)

  const playersExceptGameMaster = gameState.players.filter(player => player.id !== shootsGame.currentGameMaster.id)
  useEffect(() => {
    if (shootsGame.responses.length === playersExceptGameMaster.length) {
      setShowPercentages(true)
      setTimeout(() => {
        history.push('/round-done')
      }, 2000)
    }
  })

  const question = getFormattedQuestion(gameState, shootsGame)
  const answers = getFormattedAnswers(gameState, shootsGame)

  return <Background titlescreen align='flex-start'>
    <QuizScreen
      time={10000}
      question={question}
      answers={answers}
      correct={shootsGame.getCurrentQuestion.correct}
      percentages={showPercentages && shootsGame.responses}
      onSelect={(answerIndex, time) => {
        gameActions.slaveAnswer({
        'playerId': gameState.playerId,
        'time': time,
        'answer': answerIndex
        })
        setAnswer(answerIndex)
      }}
      onFinish={() => {
        if (!answer) {
          gameActions.slaveAnswer({
            'playerId': gameState.playerId,
            'time': 10000,
            'answer': -1
            })
        }
        setShowPercentages(true)
        setTimeout(() => {
          history.push('/round-done')
        }, 2000)
      }}
    />
  </Background>
}

SlaveQuiz.propTypes = {
  history: PropTypes.object.isRequired
}

export default SlaveQuiz
