import Start from '../components/start'
import Secret from '../components/secret'
import Login from '../components/login'

import Waiting from '../components/waiting'
import Leaderboard from '../components/leaderboard'

import Pin from '../components/pin'
import Categories from '../components/categories'
import Guest from '../components/guest'
import CountDown from '../components/status/countdown'

import GameMasterQuiz from '../components/shoots-quiz/game-master'
import SlaveQuiz from '../components/shoots-quiz/slave'
import Scores from '../components/scores'

import SlaveWaiting from '../components/status/slave-waiting'
import MasterWaiting from '../components/status/master-waiting'
import RoundDone from '../components/status/round-done'
import Winner from '../components/winner'
import TestPayment from '../components/test'
import GameEnd from '../components/game-end'
import SpectatorView from '../components/spectator'

import GameOverview from '../components/game-overview'
import MostDrinks from '../components/score-view/most-drink'

import Profile from '../components/profile'

// import { MD } from '../helpers/mobile-detect'

const defaultRoutes = [
  {
    path: '/',
    component: Start
  },
  {
    path: '/profile',
    component: Profile,
    footer: true,
    buttons: {
      leftButton: {
        title: 'footer.back'
      }
    }
  },
  {
    path: '/most-drinks',
    component: MostDrinks
  },
  {
    path: '/game-overview',
    component: GameOverview
  },
  {
    path: '/quiz-gamemaster',
    component: GameMasterQuiz,
    footer: true,
    showModal: true,
    buttons: {
      leftButton: {
        title: 'footer.back'
      }
    }
  },
  {
    path: '/quiz-slave',
    component: SlaveQuiz,
    footer: true,
    showModal: true,
    buttons: {
      leftButton: {
        title: 'footer.back'
      }
    }
  },
  {
    path: '/scores',
    component: Scores
  },
  {
    path: '/slave-waiting',
    component: SlaveWaiting
  },
  {
    path: '/master-waiting',
    component: MasterWaiting
  },
  {
    path: '/round-done',
    component: RoundDone
  },
  {
    path: '/winner',
    component: Winner
  },
  {
    path: '/test',
    component: TestPayment
  },
  {
    path: '/game-end',
    component: GameEnd
  },
  {
    path: '/spectator',
    component: SpectatorView
  },
  {
    path: '/countdown',
    component: CountDown
  },
  {
    path: '/secret',
    component: Secret,
    footer: true
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/pin',
    component: Pin,
    footer: true,
    showModal: false,
    buttons: {
      leftButton: {
        title: 'footer.back'
      }
    }
  },
  {
    path: '/guest',
    component: Guest
  },
  {
    path: '/categories',
    component: Categories,
    footer: true,
    buttons: {
      leftButton: {
        title: 'footer.back'
      }
    }
  },
  {
    path: '/leaderboard',
    component: Leaderboard,
    footer: true
  },
  {
    path: '/waiting',
    component: Waiting,
    footer: true,
    showModal: true
  }
]

export default () => ([...defaultRoutes])
