import styled from 'styled-components'
import Typography from '@material-ui/core/Typography'

const Wrapper = styled.div`
  width: 100%;
  overflow: hidden;
  background: ${({ theme }) => theme.palette.background.dark};
  padding-top: ${({ theme }) => theme.spacing(3) + 'px'};
  padding-bottom: ${({ theme }) => theme.spacing(3) + 'px'};
  padding-left: ${({ theme }) => theme.spacing(3) + 'px'};
  padding-right:0;
  margin-top: 0;
  text-align:center;
`
const StyledTitle = styled(Typography)`
  text-transform: capitalize;
`

const PlayersListWrapper = styled.div`
  width: 100%;
  overflow: hidden;
`

const CaptionBar = styled.div`
  width: 100%;
  padding: 12px 0px;
  background-color: rgba(0.1, 0.1, 0.1, 0.2);
  margin-top: 0px;
  overflow: hidden;
  display: flex;
  justify-content: center;
`

const Caption = styled(Typography)`
  padding: 0px 32px;
`

const BigPlayerImage = styled.div`
  width: 120px;
  height: 120px;
  background-image: url(${({ src }) => src});
  background-size: cover;
  margin: 0 auto;
  border-radius: 100%;
`

const PlayerListWrapper = styled.div`
  width: 100%;
  height: 100%;
  overflow: scroll;
`

const SettingsWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  overflow: hidden;
  padding: 4px 0px;
`

const OptionWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  align-content: center;
  margin-left: 24px; 
`

const ButtonWrapper = styled.div`
  overflow: hidden;
  margin-top: 8px;
`

const SettingName = styled(Typography)`
  margin-top: 0.35em !important; 
`

export {
  Wrapper,
  StyledTitle,
  PlayersListWrapper,
  CaptionBar,
  Caption,
  BigPlayerImage,
  PlayerListWrapper,
  SettingsWrapper,
  OptionWrapper,
  ButtonWrapper,
  SettingName
}
