
import React, { memo, useState, useEffect, useRef } from 'react'
import PropTypes from 'prop-types'

import { withRouter } from 'react-router'

import { UseAuthContext } from '../../context/auth/auth-context'

import styled from 'styled-components'
import { withStyles, fade, makeStyles } from '@material-ui/core/styles'

import Typography from '@material-ui/core/Typography'
import TextField from '@material-ui/core/TextField'

import { Icons } from '../../helpers/icons'
import Background from '../background'

import MaterialButton from '@material-ui/core/Button'
import Button from '../../core/button'
import getRandomName from '../../context/auth/utils/random-name'
import { useKeyPress } from '../../hooks/useKeyPress'

const Header = styled.div`
  overflow: hidden;
  margin-bottom:${props => props.theme.spacing(2) + 'px'};
`

const OptionsWrapper = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  overflow: hidden;
  padding-top: ${({ theme }) => theme.spacing(2) + 'px'};
`

const StyledGimmick = styled.div`
  opacity: 0.8;
  font-size: 12px;
  margin-top: ${({ theme }) => theme.spacing(6) + 'px'};
`

const StyledLogoWrapper = styled.div`
  overflow: hidden;
  margin-top: 40px;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
`

const useStylesGuest = makeStyles(theme => ({
  root: {
    backgroundColor: theme.palette.background.light,
    color: theme.palette.primary.dark,
    fontSize: 20,
    fontWeight: 'bold',
    width: '100%',
    textAlign: 'center',
    borderRadius: 20,
    overflow: 'hidden',
    marginBottom: theme.spacing(3),
    '&:hover': {
      backgroundColor: '#fff'
    },
    '&$focused': {
      backgroundColor: '#fff',
      boxShadow: `${fade(theme.palette.primary.main, 0.25)} 0 0 0 2px`,
      borderColor: theme.palette.primary.dark
    }
  },
  input: {
    padding: 16,
    width: '100%',
    textAlign: 'center',
    color: theme.palette.background.gray
  },
  focused: {}
}))

const styles = theme => ({
  button: { margin: theme.spacing() },
  link: {
    color: theme.palette.primary.light,
    fontSize: 14,
    'line-height': '21px',
    fontWeight: 'bold',
    '&:hover': {
      fontWeight: 700,
      color: theme.palette.primary.light
    }
  },
  gimmick: {
    fontSize: 14,
    textShadow: '2px 2px 2px rgba(150, 150, 150, 0.57)'
  }
})

const InputFile = styled.input`
 display: none;
`

const GuestTextField = (props) => {
  const classes = useStylesGuest()

  return <TextField InputProps={{ classes, disableUnderline: true }} style={{ width: '100%' }}{...props} />
}

function imageToDataUri (img, width, height) {
  // create an off-screen canvas
  let canvas = document.createElement('canvas')
  let ctx = canvas.getContext('2d')

  // set its dimension to target size
  canvas.width = width
  canvas.height = height

  // draw source image into the off-screen canvas:
  ctx.drawImage(img, 0, 0, width, height)

  // encode image to data-uri with base64 version of compressed image
  return canvas.toDataURL()
}

const Guest = ({ history, classes }) => {
  const [name, setName] = useState('')
  const [randomName, setRandomName] = useState(getRandomName())
  const [error, setError] = useState()
  const [image, setImage] = useState()
  const { setGuest } = UseAuthContext()
  const inputRef = useRef()

  const enterPressed = useKeyPress('Enter')

  const nextClick = () => {
    setGuest(name || randomName, image)
    history.push('/')
  }

  useEffect(() => {
    if (enterPressed) {
      nextClick()
    }
  }, [enterPressed])

  const checkName = (e) => {
    setError('')
    const input = e.target.value

    if (input.length > 20) {
      setError('Je naam is te lang..')
      return
    }

    setName(e.target.value)
  }

  const onImageUpload = (evt) => {
    let tgt = evt.target || window.event.srcElement

    let files = tgt.files

    // FileReader support
    if (window.FileReader && files && files.length) {
      let fr = new window.FileReader()
      fr.onload = () => {
        let img = document.createElement('img')
        // img.src = 'url(' + imgSrc + ')'
        img.src = fr.result
        img.onload = () => setImage(imageToDataUri(img, 60, 60))
      }
      fr.readAsDataURL(files[0])
    }
  }

  const uploadClick = () => {
    inputRef.current.click()
  }

  return (
    <Background align='center' showGradient showCircles>
      <StyledLogoWrapper>
        { Icons.shootsLogo }
      </StyledLogoWrapper>
      <Header>
        <Typography variant='h6' gutterBottom align='center'>
          Vul je warrior naam in. <br />
          Of genereer een random naam.
        </Typography>
      </Header>

      <GuestTextField
        InputLabelProps={{ shrink: false }}
        onChange={checkName}
        value={name}
        variant='filled'
        margin='normal'
        label=''
        placeholder={randomName}
        id='guest-name'
        helperText={error || ''}
      />

      <Button
        title='Upload image'
        background='google'
        onClick={uploadClick}
        icon={image}
      />

      <InputFile ref={inputRef} type='file' accept='image/*' id='file-input' onChange={onImageUpload} />

      <Button
        title='GA VERDER'
        background='orange'
        onClick={nextClick}
        disabled={!!((name && name.length < 3) || error)}
      />
      <OptionsWrapper>
        <MaterialButton className={classes.link} onClick={() => { history.push('/') }}>Terug</MaterialButton>
        <MaterialButton variant='text' className={classes.link} onClick={() => {
          setRandomName(getRandomName())
          setName('')
        }}>Random naam</MaterialButton>
      </OptionsWrapper>

      <StyledGimmick>
        <Typography className={classes.gimmick} variant='body1' gutterBottom align='center'>
          We slaan altijd je punten op, voor het geval je vrienden je niet geloven..
        </Typography>
      </StyledGimmick>
    </Background>
  )
}

Guest.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired
}

export default memo(withRouter(withStyles(styles)(Guest)))
