import React, { memo } from 'react'
import PropTypes from 'prop-types'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import CardActionArea from '@material-ui/core/CardActionArea'
import Typography from '@material-ui/core/Typography'

import Emoji from './../../helpers/emoji'
import styled from 'styled-components'

import { device } from './../../helpers/screens'

const EmojiWrapper = styled.div`
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  min-height: 140px;

  @media ${device.mobileL} {
    min-height: 100px;
  }
`

const StyledContent = styled(CardContent)`
  background-color: ${props => props.background + '!important'};
  height: 110px;
  overflow: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items:center;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;

  @media ${device.mobileL} {
    padding: 16px !important;
  }
`

const StyledTitle = styled(Typography)`
  text-transform: capitalize;
`

const CategoryCard = ({ id, icon, title, subtitle, color, onClick, index }) => {
  const titled = title.trim().toLowerCase()

  return (
    <Card onClick={() => { onClick(id) }}>
      <CardActionArea>
        <EmojiWrapper>
          <Emoji label={title} symbol={icon} index={index} />
        </EmojiWrapper>
        <StyledContent background={color}>
          <StyledTitle variant='h6' component='h2'>{titled}</StyledTitle>
          <Typography variant='subtitle2' component='p'>{subtitle}</Typography>
        </StyledContent>
      </CardActionArea>
    </Card>
  )
}

CategoryCard.defaultProps = {
  icon: '😌',
  title: 'Title',
  subtitle: 'Subtitle',
  color: '#F4424F'
}

CategoryCard.propTypes = {
  id: PropTypes.number.isRequired,
  icon: PropTypes.any,
  title: PropTypes.string,
  subtitle: PropTypes.string,
  color: PropTypes.string,
  onClick: PropTypes.func.isRequired,
  index: PropTypes.number
}

export default memo(CategoryCard)
