import React from 'react'
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles'

import Avatar from '@material-ui/core/Avatar'
import Grid from '@material-ui/core/Grid'

const DEFAULT_PIC = 'https://st3.depositphotos.com/6742662/16316/v/1600/depositphotos_163166948-stock-illustration-cartoon-funny-monster-halloween-vector.jpg'

const useStyles = makeStyles({
  mediumAvatar: ({ size }) => ({
    margin: 0,
    width: `${size}px` || 38,
    height: `${size}px` || 38,
    transition: `all 0.2s cubic-bezier(.17,.67,1,1.23)`,

    '&:hover': {
      cursor: 'pointer',
      transform: `scale(1.05)`
    }
  })
})

const ProfileAvatar = ({ image, onClick, size }) => {
  const classes = useStyles({ size })
  const pic = image || DEFAULT_PIC

  return (
    <Grid container justify='center' alignItems='center' onClick={onClick || (() => {})}>
      <Avatar size={size} alt='User' src={pic} className={classes.mediumAvatar} />
    </Grid>
  )
}

ProfileAvatar.propTypes = {
  image: PropTypes.string.isRequired,
  onClick: PropTypes.func,
  size: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number])
}

export default ProfileAvatar
