import React, { memo, useState } from 'react'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import PropTypes from 'prop-types'

import { Icons } from '../../helpers/icons'

import Confetti from '../../confetti/component'
import Button from '../../core/button'
import Player from '../player-status/player'

import styled, { css } from 'styled-components'
import Background from '../background'

const PlayerButton = styled(Grid)`
    margin: 8px;
`

const ShotsWrapper = styled.span`
    svg {
        ${({ disabled }) => disabled && css`
            * {
                fill: #8080804d;
            }
        `}
    }
`

const getInitalDrinks = (players) => {
  const res = {}

  players.forEach(player => {
    res[player.id] = 0
  })

  return res
}

const ServeDrinks = ({ amountOfDrinks, fuckThemUp, players, title }) => {
  const [totalDrinks, setTotalDrinks] = useState(0)
  const [playersGettingDrinks, setDrinks] = useState(() => getInitalDrinks(players))

  const addDrink = (player) => {
    if (totalDrinks < amountOfDrinks) {
      setDrinks(prev => ({
        ...prev,
        [player.id]: (prev[player.id] || 0) + 1
      }))

      setTotalDrinks(prev => prev + 1)
    }
  }

  const notEnoughDrinks = totalDrinks < amountOfDrinks

  return <Background showCircles>
    <Confetti />
    <Grid
      container
      direction='column'
      justify='center'
      alignItems='center'
      // style={{ height: '100%' }}
      >
      <Grid>
        <span role='img' aria-label='emoji' style={{ fontSize: 68 }}>🎉</span>
      </Grid>
      <Grid>
        <Typography variant='h6'>Wow!</Typography>
      </Grid>
      <Grid>
        <Typography style={{ textAlign: 'center' }} variant='h6' className='mb-4 pb-2'>{title}</Typography>
      </Grid>
      <Grid>
        <Typography>Je mag {Array(amountOfDrinks).fill().map((_, i) => <ShotsWrapper disabled={i < totalDrinks}>{Icons.smallDrink}</ShotsWrapper>)} uitdelen.</Typography>
      </Grid>
      <Grid>
        <Typography>Tap op een speler om een <b>drankje</b> te geven.</Typography>
      </Grid>
      <Grid className='mb-4'>
        <Typography><b>Meerdere taps</b> zijn meerdere drankjes.</Typography>
      </Grid>
      <Grid className='mb-4'>
        <Grid container style={{}}>
          {players.map((player, i) => {
            return <PlayerButton onClick={() => addDrink(player)}>
              <Player size={'64px'} player={player} count={playersGettingDrinks[player.id] || 0} notEnoughDrinks={notEnoughDrinks} borderColorIndex={!notEnoughDrinks ? 2 : i} />
            </PlayerButton>
          })}
        </Grid>
      </Grid>

      <Button
        title='Fuck em up!'
        background='pink'
        onClick={() => fuckThemUp(playersGettingDrinks)}
        disabled={notEnoughDrinks}
        upperCase
        //   variant={notEnoughDrinks ? 'disabled' : 'undefined'}
        />

    </Grid>
  </Background>
}

ServeDrinks.propTypes = {
  amountOfDrinks: PropTypes.number,
  fuckThemUp: PropTypes.func.isRequired,
  players: PropTypes.array,
  title: PropTypes.string
}

ServeDrinks.defaultProps = {
  amountOfDrinks: 4,
  title: 'Je bent de winnaar!'
}

export default memo(ServeDrinks)
