import React from 'react'

import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider'
import { ThemeProvider } from 'styled-components'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'

import AuthContextProvider from './context/auth'
import ShootsContextProvider from './context/shoots-context'

import ErrorBoundary from './error-boundary'
import Route404 from './route-404'

import Page from './hoc/layout'
import getRoutes from './routes'
import theme from './helpers/theme'
import ColorMetaTags from './color-meta-tags'
import './App.css'

// import * as Sentry from '@sentry/browser'

// // Error Logger
// Sentry.init({ dsn: 'https://aaffb72ecb344c91a453f155a6dfccfd@sentry.io/1815612' })

const App = () => {
  const routes = getRoutes()

  // useEffect(() => {
  //   let elem = document.body

  //   if (!document.fullscreenElement) {
  //     elem.requestFullscreen().catch(err => {
  //       console.log(`Error attempting to enable full-screen mode: ${err.message} (${err.name})`)
  //     })
  //   } else {
  //     document.exitFullscreen()
  //   }
  // }, [])

  const routeComponents = routes.map(
    ({ path, component, footer, header, showModal, buttons }, key) => (
      <Page
        exact
        path={path}
        component={component}
        key={key}
        includeHeader={header}
        includeFooter={footer}
        showModal={showModal}
        buttons={buttons} />
    )
  )

  return (
    <MuiThemeProvider theme={theme}>
      <ThemeProvider theme={theme}>
        <ColorMetaTags>
          <Router basename={process.env.PUBLIC_URL}>
            <AuthContextProvider>
              <ShootsContextProvider>
                <ErrorBoundary>
                  <Switch>
                    {
                      routeComponents
                    }
                    <Route component={Route404} />
                  </Switch>
                </ErrorBoundary>
              </ShootsContextProvider>
            </AuthContextProvider>
          </Router>
        </ColorMetaTags>
      </ThemeProvider>
    </MuiThemeProvider>
  )
}

export default App
