import React, { memo, useEffect, useState } from 'react'

import Typography from '@material-ui/core/Typography'
import Background from '../background'
import styled from 'styled-components'
import { useShootsContext } from '../../context/shoots-context'
import DrinkModal from '../scores/drinks-modal'

const Wrapper = styled.div`
  width: 100%;
  overflow: hidden;
  background: ${({ theme }) => theme.palette.background.dark};
  padding: ${({ theme }) => theme.spacing(3) + 'px'};
  text-align:center;
`

const GameOverview = () => {
  const { gameState } = useShootsContext()
  const [showModal, setShowModal] = useState(false)

  const drinks = gameState.mostDrinks && gameState.mostDrinks[gameState.playerId]

  useEffect(() => {
    if (drinks) {
      setShowModal(true)
    }
  }, [drinks])

  return <Background showCircles alignItems='center' noPadding>
    <DrinkModal
      amountOfDrinks={drinks} open={showModal} doneClick={() => setShowModal(false)}
    />
    <button onClick={() => setShowModal(true)}>Open</button>
    <Wrapper>
      <Typography variant='h6' gutterBottom align='center'>
        Wacht even.
      </Typography>
    </Wrapper>
  </Background>
}

export default memo(GameOverview)
