import React, { memo } from 'react'
import PropTypes from 'prop-types'

import { withStyles } from '@material-ui/core/styles'

import Typography from '@material-ui/core/Typography'
import TextField from '@material-ui/core/TextField'

import { useShootsContext } from '../../../context/shoots-context'
import { Wrapper, StyledTitle } from '../styled'

const styles = theme => ({
  root: {
    marginRight: theme.spacing(),
    marginLeft: theme.spacing()
  },
  input: {
    background: theme.palette.background.dark,
    color: theme.palette.primary.light,
    fontSize: 32,
    width: 160,
    textAlign: 'center',
    letterSpacing: theme.spacing()
  }
})

const Pin = ({ classes }) => {
  const { gameState } = useShootsContext()

  return (
    <Wrapper>
      <StyledTitle variant='h6'>{gameState.categoryName}</StyledTitle>
      <Typography variant='h6' gutterBottom align='center'>
          Nodig je vrienden uit met deze pin
      </Typography>
      <TextField
        inputProps={{ className: classes.input, maxLength: 5 }}
        InputLabelProps={{ shrink: false }}
        value={gameState.pin}
        variant='outlined'
        margin='normal'
        label=''
        id='pin-control'
        disabled
      />
    </Wrapper>
  )
}

Pin.propTypes = {
  classes: PropTypes.object
}

export default memo(withStyles(styles)(Pin))
