import React from 'react'
import PropTypes from 'prop-types'

import { withRouter } from 'react-router'
import { ShootsContext } from './context/shoots-context'
import Background from './components/background'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Button from './core/button'
import Text from './core/translations'

import LogRocket from 'logrocket'
LogRocket.init('ucoydh/shoots')

const isLocalhost = Boolean(
  window.location.hostname === 'localhost' ||
      // [::1] is the IPv6 localhost address.
      window.location.hostname === '[::1]' ||
      // 127.0.0.1/8 is considered localhost for IPv4.
      //eslint-disable-line
      window.location.hostname.match(/^127(?:\.(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}$/)
)

class ErrorBoundary extends React.Component {
  constructor (props) {
    super(props)
    this.state = { hasError: false }
  }

  static contextType = ShootsContext

  static getDerivedStateFromError (error) {
    // Update state so the next render will show the fallback UI.
    return { hasError: true, error }
  }

  componentDidCatch (error, errorInfo) {
    // You can also log the error to an error reporting service
    this.context.pushNotification(({
      title: 'Fuck!!!!',
      message: 'Weird shit happend to your game.. ' + error.toString(),
      type: 'danger'
    }))
    // Sentry.captureException(error)
    if (!isLocalhost) {
      LogRocket.captureException(error)
    }
    console.error(error)

    this.props.history.push('/')
  }

  render () {
    if (this.state.hasError) {
      // You can render any custom fallback UI
      return <Background showCircles>
        <Grid
          container justify='center'
          alignItems='center'
          style={{ height: '100%' }}
          >
          <Grid>
            <Typography variant='h6'><Text text='error.title' /></Typography>
            <Typography><Text text='error.message' /></Typography>
            <Button title={<Text text='error.back' />} background='pink' onClick={() => { document.location.reload() }} />
          </Grid>
        </Grid>
      </Background>
    }

    return this.props.children
  }
}

ErrorBoundary.propTypes = {
  children: PropTypes.node.isRequired,
  history: PropTypes.object.isRequired
}

export default withRouter(ErrorBoundary)
