import React from 'react'
import PropTypes from 'prop-types'

import Background from './components/background'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Button from './core/button'
import Text from './core/translations'

const Route404 = ({ history }) => {
  return <Background showCircles>
    <Grid
      container justify='center'
      alignItems='center'
      style={{ height: '100%' }}
    >
      <Grid>
        <Typography variant='h6'><Text text='route404.title' /></Typography>
        <Typography><Text text='route404.message' /></Typography>
        <Button title={<Text text='route404.back' />} background='pink' onClick={() => { history.push('/') }} />
      </Grid>
    </Grid>
  </Background>
}

Route404.propTypes = {
  history: PropTypes.object
}

export default Route404
