import React from 'react'
import TextField from '@material-ui/core/TextField'
import { makeStyles } from '@material-ui/core/styles'
import Capitalize from 'lodash/capitalize'
import Text from '../../core/translations'

import Grid from '@material-ui/core/Grid'

require('dotenv').config()

const useStyles = makeStyles(theme => ({
  wrapper: {
    backgroundColor: 'rgba(0, 0, 0, 0.07)',
    margin: '0px',
    padding: theme.spacing(),
    position: 'relative',
    display: 'flex',
    borderRadius: theme.spacing(1)
  },
  container: {
    marginBottom: -theme.spacing() + 'px'
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    textTranform: 'capitalize',
    width: '100%'
  },
  resize: {
    fontSize: 12,
    textTranform: 'capitalize',
    color: theme.palette.black,
    fontWeight: 600
  }
}))

const getAppName = () => {
  return Capitalize(process.env.REACT_APP_NAME)
}

const Version = () => {
  const classes = useStyles()
  const appName = getAppName()

  console.log(`appname: ${process.env.REACT_APP_NAME} version: ${process.env.REACT_APP_VERSION}`)

  return <div className={classes.wrapper}>
    <Grid container>
      <Grid align='left' item xs={12} className={classes.container}>
        <TextField
          disabled
          id='standard-disabled'
          label={<Text text='version.name' />}
          defaultValue={appName || 'shoots'}
          className={classes.textField}
          margin='normal'
          InputLabelProps={{ style: { textTransform: 'normal' } }}
          InputProps={{
            style: { },
            classes: { input: classes.resize },
            disableUnderline: true
          }}
         />
      </Grid>
      <Grid align='left' item xs={12} className={classes.container}>
        <TextField
          disabled
          id='standard-disabled'
          label={<Text text='version.version' />}
          defaultValue={process.env.REACT_APP_VERSION || '1.0'}
          className={classes.textField}
          margin='normal'
          InputLabelProps={{ style: { textTransform: 'normal' } }}
          InputProps={{
            style: { },
            classes: { input: classes.resize },
            disableUnderline: true
          }}
        />
      </Grid>
    </Grid>
  </div>
}

export default Version
