import React, { memo, useEffect } from 'react'
import PropTypes from 'prop-types'

import { isDebug, getPlayersTopThree } from '../../helpers/shoots-helper'
import { useShootsContext } from '../../context/shoots-context'
import ServeDrinks from './serve-drinks'

const Winner = ({ history }) => {
  const { gameState, shootsGame, gameActions, getWinner } = useShootsContext()

  const playersDrinkTopThree = getPlayersTopThree(gameState.players, shootsGame.responses, shootsGame.getCurrentQuestion, getWinner())

  const DEBUG_MODE = isDebug()

  useEffect(() => {
    if (shootsGame.drinks && Object.keys(shootsGame.drinks).length > 0 && !DEBUG_MODE) {
      history.push('/scores')
    }
  })

  return <ServeDrinks players={playersDrinkTopThree} fuckThemUp={(drinks) => gameActions.fuckThemUp(drinks)} />
}

Winner.propTypes = {
  history: PropTypes.object.isRequired
}

export default memo(Winner)
