import React, { memo, useEffect } from 'react'
import PropTypes from 'prop-types'

import { sortPlayersOnMostDrinks } from '../../helpers/shoots-helper'
import { useShootsContext } from '../../context/shoots-context'
import ServeDrinks from '../winner/serve-drinks'

const MostDrinks = ({ history }) => {
  const { gameState, shootsGame, actions } = useShootsContext()

  const mostDrinkPlayers = sortPlayersOnMostDrinks(gameState.players, shootsGame)

  const fuckThemUp = (drinks) => {
    actions.updateAll({ mostDrinks: drinks })
  }

  useEffect(() => {
    if (gameState.mostDrinks) {
      history.push('/game-overview')
    }
  }, [gameState.mostDrinks])

  return <ServeDrinks
    players={mostDrinkPlayers.slice(-3)}
    fuckThemUp={fuckThemUp}
    title='Jij hebt het meeste gedronken in de game..'
  />
}

MostDrinks.propTypes = {
  history: PropTypes.object.isRequired
}

export default memo(MostDrinks)
