import PropTypes from 'prop-types'
import React, { memo } from 'react'
import languages from '../../core/translations/languages'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import { useShootsContext } from '../../context/shoots-context'
import Text from '../../core/translations'
import Typography from '@material-ui/core/Typography'
// import styled from 'styled-components'

const SelectLang = ({ textClass }) => {
  const { lang, setLang } = useShootsContext()

  return <FormControl variant='outlined'>
    <InputLabel htmlFor='outlined-age-simple'>
      <Typography variant='body1' className={textClass}>
        <Text text='settings.language' />
      </Typography>
    </InputLabel>
    <Select
      value={lang}
      onChange={(e) => {
        const language = e.target.value
        window.localStorage.setItem('customLang', language)
        setLang(language)
      }}
      // labelWidth={labelWidth}
      variant='outlined'
      inputProps={{
      name: 'language',
      id: 'outlined-age-simple',
      variant: 'outlined'
    }}
  >
      {
        Object.entries(languages).map(([key, value], index) => {
          return <MenuItem key={`${key}-${index}`} value={value}>
            <Typography variant='body1' className={textClass}>
              {key}
            </Typography>
          </MenuItem>
        })
      }
    </Select>
  </FormControl>
}

SelectLang.propTypes = {
  textClass: PropTypes.string
}

export default memo(SelectLang)
