import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'

// unicode not working, so do it like this
const renderEmoji = (index) => [
  '😌',
  '💦',
  '🤪',
  '🤔'
][index]

const Styledspan = styled.span(props => ({
  fontSize: props.size,
  // unicode not working
  '&:after': {
    content: props.symbol
  }
}))

const Emoji = ({ label, symbol, size, index }) => (
  <Styledspan
    className='emoji'
    aria-label={label}
    aria-hidden={false}
    role='img'
    symbol={symbol}
    size={size}
    >
    {renderEmoji(index)}
  </Styledspan>
)

Emoji.defaultProps = {
  label: 'emoji',
  symbol: 'U+1F601',
  size: '40px'
}

Emoji.propTypes = {
  label: PropTypes.string,
  symbol: PropTypes.string.isRequired,
  size: PropTypes.string,
  index: PropTypes.number
}

export default Emoji
