
import React, { memo, useState, useCallback, useEffect } from 'react'
import PropTypes from 'prop-types'

import styled from 'styled-components'

import TextField from '@material-ui/core/TextField'

import { withStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'

import Background from '../background'

import { UseAuthContext } from '../../context/auth'
import { useShootsContext } from '../../context/shoots-context'
import GameCard from '../game-card'

import { withRouter } from 'react-router'

import Grid from '@material-ui/core/Grid'
import Text from './../../core/translations'

const Header = styled.div`
  overflow: hidden;
  margin-bottom:${props => props.theme.spacing(4) + 'px'};
  margin-top: -150px;
`

const styles = theme => ({
  root: {
    marginRight: theme.spacing(),
    marginLeft: theme.spacing()
  },
  input: {
    background: theme.palette.background.dark,
    color: theme.palette.primary.light,
    fontSize: 40,
    width: 210,
    textAlign: 'center',
    letterSpacing: theme.spacing()
  },
  button: { margin: theme.spacing() }
})

const Pin = ({ classes, history }) => {
  const { name } = UseAuthContext()
  const { actions, gameState, ip } = useShootsContext()
  const [pin, setPin] = useState('')

  useEffect(() => {
    if (pin.length === 5) {
      playerJoinGame()
    }
  }, [pin])

  useEffect(() => {
    const localStoragePin = window.localStorage.getItem('gamePin')
    if (localStoragePin) {
      playerJoinGame(localStoragePin)
    }
  }, [])

  useEffect(() => {
    if (ip) {
      actions.setIp({ ip })
    }
  }, [ip])

  const playerJoinGame = useCallback((pinInput) => {
    let idFromLocal = window.localStorage.getItem('player-id')
    if (!idFromLocal) {
      const randomKey = Math.random().toString(36).substring(7)
      window.localStorage.setItem('player-id', randomKey)
      idFromLocal = randomKey
    }
    actions.playerJoin({
      name: name,
      pin: parseInt(pinInput || pin),
      playerId: idFromLocal
    })
  })

  console.log(gameState.localGames)

  return (
    <Background align='center'>
      <Header>
        <Typography variant='h6' gutterBottom align='center'>
          <Text text='pin.fillIn' />
        </Typography>
      </Header>
      <TextField
        inputProps={{ className: classes.input, maxLength: 5 }}
        InputLabelProps={{ shrink: false }}
        onChange={evt => setPin(evt.target.value)}
        value={pin}
        variant='outlined'
        margin='normal'
        label=''
        id='pin-control'
      />
      {
        gameState.localGames && gameState.localGames.map((game, index) => {
          return <Grid key={`${game.pin}-${index}`} container spacing={4} className='mt-4'>
            <GameCard
              hostName={game.hostName}
              hostPicture={game.players.find(p => p.isHost).image}
              playerCount={game.players.length}
              onClick={() => playerJoinGame(game.pin)}
          />
          </Grid>
        })
      }
    </Background>
  )
}

Pin.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired
}

export default withRouter(memo(withStyles(styles)(Pin)))
