import React, { useState, useEffect } from 'react'

import { withRouter } from 'react-router'
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types'

import { UseAuthContext } from '../../context/auth/auth-context'
import { useShootsContext } from '../../context/shoots-context'
import { GoogleButton, GuestButton, FaceBookButton } from '../../context/auth'

import { withStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'

import Background from '../background'

import Button from '../../core/button'

import SVGIcon from '../status/svg-icon'
import { Icons } from '../../helpers/icons'

import VolumeControl from '../volume-control'
import ProfileAvatar from '../avatar'

import InstructionsDialog from '../instructions-stepper'
import tutorialSteps from '../instructions-stepper/instructions'

import SettingsDialog from '../settings-dialog'
import Slide from '@material-ui/core/Slide'

import { CORE } from '../../core/colors'
import { MD } from '../../helpers/mobile-detect'

import CommingSoonModal from '../comming-soon-modal'
import MobileOrientationCheck from './mobile'

import Text from './../../core/translations'

import {
  UltimateWrapper,
  ButtonsHolder,
  AvatarWrapper,
  Volume,
  RoundButtonWrapper,
  StyledLogoWrapper
} from './styled'

const styles = theme => ({
  button: { margin: theme.spacing(1) },
  link: {
    color: theme.palette.primary.light,
    fontSize: 14,
    lineHeight: '21px',
    fontWeight: 600,
    position: 'relative',

    '&:hover': {
      color: theme.palette.primary.light
    },

    '&::after': {
      content: `''`,
      position: 'absolute',
      left: '0px',
      bottom: '-2px',
      width: '0',
      height: '0',
      background: theme.palette.primary.light,
      transition: 'width .5s ease, background-color .5s ease, left .5s ease;'
    },

    '&:hover::after': {
      width: '100%',
      left: 0,
      height: 1
    }
  },
  drinks: {
    color: `${theme.palette.primary.black} !important`,
    fontSize: 60,
    fontWeight: '600'
  },
  content: {
    textAlign: 'center',
    padding: 0,
    lineHeight: '26px',
    color: `${theme.palette.primary.black} !important`
  }
})

const ICON_SIZE = 58

const Transition = React.forwardRef(function Transition (props, ref) {
  return <Slide direction='up' ref={ref} {...props} />
})

const Start = ({ classes, history }) => {
  const { name, image, google, facebook, guest, authenticated } = UseAuthContext()
  const [ openSettingsDialog, setSettingsDialog ] = useState(false)
  const [ showComingSoon, setShowComingSoon ] = useState(false)

  const { gameState, destroyGame } = useShootsContext()

  const [open, setOpen] = useState(false)

  useEffect(() => {
    destroyGame()
  }, [])

  console.log('AUTHENTICATION --->', name, image, google, facebook, guest)
  console.table('CONTEXT --->', gameState)

  const handleClose = () => {
    setOpen(false)
  }

  const handleSettingsClose = () => {
    setSettingsDialog(false)
  }

  return (<React.Fragment>
    <Background showCircles showGradient titlescreen align='center' fadeIn>
      <CommingSoonModal open={showComingSoon} onClose={() => setShowComingSoon(false)} />
      <UltimateWrapper>
        {
          authenticated && <>
            <AvatarWrapper>
              <ProfileAvatar
                image={image}
                onClick={() => { history.push('/profile') }}
              />
            </AvatarWrapper>
            <Volume>
              <VolumeControl />
            </Volume>
          </>
        }

        <StyledLogoWrapper>
          { Icons.shootsLogo }
        </StyledLogoWrapper>

        <ButtonsHolder id='menu-list'>
          {
          authenticated ? <React.Fragment>
            <Button
              title={<Text text='home.newGame' />}
              background='orange'
              onClick={() => { history.push('/categories') }}
              upperCase
            />

            <Button
              title={<Text text='home.pin' />}
              background='blue'
              onClick={() => { history.push('/pin') }}
              upperCase
            />

            <Button
              title={<Text text='home.instructions' />}
              background='pink'
              onClick={() => { setOpen(true) }}
              upperCase
            />

            <RoundButtonWrapper>
              <SVGIcon size={ICON_SIZE} icon={Icons.shoppingCart} color={CORE.BLUE} strokeColor={CORE.TRANSPARANT} strokeWidth={2} onClick={() => setShowComingSoon(true)} />
              <SVGIcon size={ICON_SIZE} icon={Icons.leaderboard} color={CORE.GREEN} strokeColor={CORE.TRANSPARANT} strokeWidth={2} onClick={() => setShowComingSoon(true)} />
              <SVGIcon size={ICON_SIZE} icon={Icons.settings} color={CORE.YELLOW} strokeColor={CORE.TRANSPARANT} strokeWidth={2} onClick={() => setSettingsDialog(true)} />
            </RoundButtonWrapper>

          </React.Fragment> : <React.Fragment>
            <FaceBookButton />
            <GoogleButton />
            <GuestButton />
          </React.Fragment>
          }
        </ButtonsHolder>

        <Typography variant='body1' gutterBottom align='center'>
          <Link className={classes.link} to='/'>SHOOTS&trade;</Link>&nbsp;
          <Text text='home.isDeveloped' />&nbsp;<Link className={classes.link} to='#'><b> Whipcat</b></Link>{' '}<Text text='home.and' />{' '}<Link className={classes.link} to='#'><b>Dropsaus</b></Link>
        </Typography>

        <InstructionsDialog open={open} steps={tutorialSteps} transition={Transition} onClose={handleClose} fullScreen={MD()} maxWidth='xs' />
        <SettingsDialog open={openSettingsDialog} onClose={handleSettingsClose} maxWidth='xs' />

      </UltimateWrapper>

    </Background>
    <MobileOrientationCheck />
  </React.Fragment>
  )
}

Start.propTypes = {
  history: PropTypes.object,
  classes: PropTypes.object.isRequired
}

export default withRouter(withStyles(styles)(Start))
