import React, { memo } from 'react'
import PropTypes from 'prop-types'

import Modal from '../modal'
import { Icons } from '../../helpers/icons'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import styled from 'styled-components'
import { withStyles } from '@material-ui/core/styles'

const DrinksWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: space-around;
  margin-bottom: 28px;
`
const styles = theme => ({
  container: {
    margin: `0 !important`
  },
  drinks: {
    color: `${theme.palette.primary.black} !important`,
    fontSize: 60,
    fontWeight: '600'
  },
  content: {
    textAlign: 'center',
    padding: 0,
    lineHeight: '26px',
    color: `${theme.palette.primary.black} !important`
  }
})

const DrinksModal = ({ classes, open, doneClick, amountOfDrinks }) => {
  const modalContent = {
    title: amountOfDrinks > 0 ? 'AH SH🤢🤢T..' : 'Phew!',
    content: <>
      <Grid container className={classes.container} alignItems='center' justify='center'>
        {
          amountOfDrinks > 0
          ? <>
            <DrinksWrapper>
              <Grid item xs={6}>
                {Icons.drink}
              </Grid>
              <Grid item xs={6}>
                <Typography className={classes.drinks}> &times;{amountOfDrinks}</Typography>
              </Grid>
            </DrinksWrapper>
            <Typography className={classes.content}>
              Als je klaar bent met drinken, klik je op <b>Ready</b> om aan te geven dat je klaar bent voor de volgende ronde!
            </Typography>
          </>
        : <Grid item xs={6}>
          <Typography className={classes.content}>
              Je hoeft niet te drinken!
          </Typography>
        </Grid>
      }
      </Grid>
    </>,
    buttons: [
      {
        title: 'Ready!',
        color: 'green',
        onClick: doneClick
      }
    ]
  }

  return (
    <Modal isShowing={open} content={modalContent} maxWidth='xs' onClose={doneClick} />
  )
}

DrinksModal.propTypes = {
  open: PropTypes.bool,
  amountOfDrinks: PropTypes.number,
  doneClick: PropTypes.func,
  classes: PropTypes.object
}

export default memo(withStyles(styles)(DrinksModal))
