import React, { useState } from 'react'

import PropTypes from 'prop-types'
import Background from '../background'
import QuizScreen from '../quiz/quiz-screen'

import { useShootsContext } from '../../context/shoots-context'

import { getFormattedQuestion, getFormattedAnswers } from '../../helpers/shoots-helper'

const GameMaster = ({ history }) => {
  // not game master
  const { gameState, shootsGame, gameActions } = useShootsContext()
  const [answer, setAnswer] = useState(null)

  if (shootsGame.currentGameMaster.id !== gameState.playerId) {
    console.error('Player is not the game master, redirect?')
    return null
  }

  const question = getFormattedQuestion(gameState, shootsGame)
  const answers = getFormattedAnswers(gameState, shootsGame)

  return <Background titlescreen align='flex-start'>
    <QuizScreen
      time={10000}
      question={question}
      answers={answers}
      // question={shootsGame.getCurrentQuestion.title}
      // answers={shootsGame.getCurrentQuestion.answers}
      // correct={shootsGame.getCurrentQuestion.correct}
      onSelect={(answerIndex, time) => {
        setAnswer(answerIndex)
        gameActions.gameMasterAnswer(answerIndex)
        history.push('/master-waiting')
      }}
      onFinish={() => {
        if (!answer) {
          gameActions.gameMasterAnswer(Math.round(Math.random() * answers.length - 1))
        }
        history.push('/master-waiting')
      }}
    />
  </Background>
}

GameMaster.propTypes = {
  history: PropTypes.object.isRequired
}

export default GameMaster
