import React, { memo, useState } from 'react'
import PropTypes from 'prop-types'

import Typography from '@material-ui/core/Typography'
import Settings from './settings'
import TextField from '@material-ui/core/TextField'

import Button from '../../../core/button'
import Text from '../../../core/translations'

import { fade, makeStyles } from '@material-ui/core/styles'

import {
  Wrapper,
  SettingsWrapper,
  OptionWrapper,
  ButtonWrapper,
  SettingName
} from './..//styled'

// TODO PUT IN OWN COMPONENT TEXTFIELD + STYLE
const useStylesSettings = makeStyles(theme => ({
  root: {
    backgroundColor: theme.palette.background.light,
    color: theme.palette.primary.dark,
    fontSize: 20,
    fontWeight: 'bold',
    width: '100%',
    textAlign: 'center',
    borderRadius: 20,
    overflow: 'hidden',
    marginLeft: 0,

    '&:hover': {
      backgroundColor: '#fff'
    },
    '&$focused': {
      backgroundColor: '#fff',
      boxShadow: `${fade(theme.palette.primary.main, 0.25)} 0 0 0 2px`,
      borderColor: theme.palette.primary.dark
    }
  },
  input: {
    padding: 16,
    width: '100%',
    textAlign: 'center',
    color: theme.palette.background.gray
  },
  disabled: {
    backgroundColor: theme.palette.background.light,
    color: theme.palette.primary.dark
  },
  focused: {}
}))

const SettingsTextField = (props) => {
  const classes = useStylesSettings()

  return (
    <TextField InputProps={{
      classes,
      disableUnderline: true
    }}
      style={{ width: '30%' }}
      {...props}
    />)
}

const GameSettings = ({ description }) => {
  const [error, setError] = useState()
  const [settings, saveSettings] = useState(JSON.parse(window.localStorage.getItem('game-settings')) || Settings)

  const updateSetting = (index, type, setting) => e => {
    const { name, value, min, max } = setting
    setError('')

    console.log('index: ', index)
    console.log('property: ', name)

    let newValue = value

    if (type.toLowerCase() === 'minus') {
      newValue = newValue -= 1
    } else {
      newValue = newValue += 1
    }

    const validInput = inRange(setting, newValue)

    if (!validInput) {
      return
    }

    let newArr = [...settings]
    newArr[index] = { name: name, value: newValue, min: min, max: max }

    saveSettings(newArr)
    window.localStorage.setItem('game-settings', JSON.stringify(newArr))
  }

  const inRange = (setting, input) => {
    const { min, max } = setting
    return (input >= min && input <= max) // ((input - min) * (input - max) <= 0)
  }

  console.log('game-settings', settings)

  const getOptions = () => {
    const options = settings.map((setting, index) => {
      const { name, value } = setting

      return (
        <SettingsWrapper key={`setting_${index}`}>
          <SettingName variant='h6' align='center'>
            { name }
          </SettingName>
          <OptionWrapper>
            <ButtonWrapper>
              <Button
                color='transparant'
                onClick={updateSetting(index, 'minus', setting)}
                title={<Text text='-' />}
                variant='flat'
                fontSize={24}
            />
            </ButtonWrapper>
            <SettingsTextField
              InputLabelProps={{ shrink: false }}
              onChange={() => { console.log('nope') }}
              disabled
              name={name}
              value={value}
              variant='filled'
              margin='normal'
              label=''
              placeholder='0'
              id={`setting_${index}`}
              helperText={error || ''}
            />
            <ButtonWrapper>
              <Button
                color='transparant'
                onClick={updateSetting(index, 'plus', setting)}
                title={<Text text='+' />}
                variant='flat'
                fontSize={24}
            />
            </ButtonWrapper>
          </OptionWrapper>
        </SettingsWrapper>
      )
    })

    return options
  }

  return (
    <>
      <Wrapper>
        <Typography variant='subtitle2' gutterBottom align='center'>
          { description || 'Pas hier de algemene spel instellingen aan. De instellingen worden opgeslagen voor elke andere game. Je kunt uiteraard deze hier altijd wijzingen.' }
        </Typography>
        {
          getOptions()
        }
      </Wrapper>
    </>
  )
}

GameSettings.propTypes = {
  description: PropTypes.string
}

export default memo(GameSettings)
