
import React, { memo, useEffect } from 'react'
import PropTypes from 'prop-types'

import AppBar from '@material-ui/core/AppBar'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'

import { useShootsContext } from '../../context/shoots-context'
import { isDebug } from '../../helpers/shoots-helper'

import PlayersList from '../players-list'
import { PlayerListWrapper } from './styled'

import Pin from './pin'
import GameSettings from './game-settings'

const TabContainer = (props) => {
  return (
    <PlayerListWrapper>
      {props.children}
    </PlayerListWrapper>
  )
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired
}

const LinkTab = (props) => {
  return (
    <Tab
      component={'a'}
      onClick={event => { event.preventDefault() }}
      classes={{
        textColor: 'white'
      }}
      {...props}
    />
  )
}

LinkTab.propTypes = {
  classes: PropTypes.object
}

const Waiting = ({ classes, history }) => {
  const [activeTab, setActiveTab] = React.useState(0)
  const { gameState, shootsGame, isHost } = useShootsContext()
  const DEBUG_MODE = isDebug()

  useEffect(() => {
    if (!DEBUG_MODE) {
      if (shootsGame.currentGameMaster) {
        history.push('/countdown')
      }
      if (gameState.gameStarted) {
        history.push('/scores')
      }
    }
  }, [shootsGame.currentGameMaster])

  const handleChange = (event, newValue) => {
    setActiveTab(newValue)
  }

  useEffect(() => {
    if (!isHost) {
      if (gameState.pin) {
        window.localStorage.setItem('gamePin', gameState.pin)
      } else {
        if (window.localStorage.getItem('gamePin')) {
          history.push('/pin')
        }
      }
    }
  }, [])

  return (
    <>
      <Pin />
      <AppBar position='static'>
        {
          isHost && <Tabs variant='fullWidth' value={activeTab} onChange={handleChange}>
            <LinkTab label='Spelers' href='/waiting' classes={classes} />
            <LinkTab label='Spelinstellingen' href='/game-settings' />
          </Tabs>
        }
      </AppBar>
      {activeTab === 0 && (
      <TabContainer>
        {
          gameState.players &&
            <PlayersList
              waiting
              players={gameState.players}
            />
        }
      </TabContainer>)}
      {
        activeTab === 1 && (
        <TabContainer>
          <GameSettings />
        </TabContainer>
        )}
    </>
  )
}

Waiting.propTypes = {
  classes: PropTypes.object,
  history: PropTypes.object.isRequired
}

export default memo(Waiting)
