import { memo, useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import get from 'lodash/get'
import Translations from './translations'
import axios from 'axios'
import apiUrl from '../../context/api'
import { useShootsContext } from '../../context/shoots-context'

const Text = ({ text }) => {
  const { lang } = useShootsContext()
  const [response, setResponse] = useState()

  useEffect(() => {
    // if not in local translations
    if (!Translations[lang]) {
      const dutchText = get(Translations, 'nl.' + text)

      axios.post(apiUrl + '/api/translate', {
        from: 'nl',
        to: lang,
        text: dutchText
      })
        .then(function (response) {
          // setTextIfRelevant(response.data.translation, response.data.lang)
          setResponse(response.data)
        })
    }
  }, [lang])

  const textFromResponse = response && response.translation

  // get current translation if exists else get it from local if exists else get previous reponse if exists else get dutch translation
  return response && response.lang === lang ? textFromResponse : get(Translations, lang + '.' + text) || textFromResponse || get(Translations, 'nl.' + text) || text
}

Text.propTypes = {
  text: PropTypes.string
}

export default memo(Text)
