import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import { withRouter } from 'react-router'
import { Funny } from './messages'
import { useShootsContext } from '../../context/shoots-context'

import useGetLocalStorageItem from '../../hooks/useGetLocalStorage'

import Status from './status'
import WaitIcon from './wait-timer'

const CountDown = ({ history }) => {
  const { isGameMaster, shootsGame } = useShootsContext()
  const [index, setIndex] = useState(0)
  const [timerPause, setTimerPause] = useState(false)
  let waitingTime = shootsGame.waitingTime

  const gameMaster = isGameMaster()

  if (!waitingTime) {
    const settingsLocal = useGetLocalStorageItem('game-settings')
    waitingTime = settingsLocal
      ? settingsLocal.find(x => x.name.toLowerCase() === 'wachttijd').value
      : 100 // should never happen 100 seconds
  }

  useEffect(() => {
    const interval = setInterval(() => {
      setIndex(index + 1)
    }, 2000)
    return () => clearInterval(interval)
  }, [])

  const startGame = () => {
    if (gameMaster) {
      history.push('/quiz-gamemaster')
    } else {
      history.push('/slave-waiting')
    }
  }

  const funnyMessages = Funny.slave[index]

  const setTimerState = () => {
    setTimerPause(!timerPause)
  }
  console.log('TIMER', waitingTime)

  return (
    <Status
      title='Momentje'
      caption={funnyMessages}
      icon={<WaitIcon onFinish={startGame} time={waitingTime} timerPause={timerPause} />}
      setTimerState={setTimerState}
      time={waitingTime}
      timerState={timerPause}
    />
  )
}

CountDown.propTypes = {
  history: PropTypes.object
}

export default withRouter(CountDown)
