import styled from 'styled-components'

/* REFACTOR 'absolute' otherwise text will disappear */
const UltimateWrapper = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 50%;
  left: 50%;
  width: 80%;
  transform: translate(-50%, -50%);
`

const ButtonsHolder = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 24px 0px 64px 0px;
  width: 100%;
`

const AvatarWrapper = styled.div`
  position: absolute;
  top: 20px;
  right: 0px;
  cursor: pointer;
`

const Volume = styled.div`
  position: absolute;
  top: 5px;
  left: -10px;
  cursor: pointer;
`

const RoundButtonWrapper = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  padding: 24px;
`

const StyledLogoWrapper = styled.div`
  overflow: hidden;
  margin-top: 80px;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
`

export {
  UltimateWrapper,
  ButtonsHolder,
  AvatarWrapper,
  Volume,
  RoundButtonWrapper,
  StyledLogoWrapper
}
