const getAllDrinksFor = (id, shootsGame) => {
  let res = 0
  shootsGame.drinkHistory && shootsGame.drinkHistory.forEach(drinks => {
    if (drinks[id]) {
      res += drinks[id]
    }
  })
  return res || 0
}

const GameEnd = ({ history }) => {
  const { actions, isHost, gameState, shootsGame } = useShootsContext()

  const drinks = {}

  gameState.players.forEach(player => {
    drinks[player.id] = getAllDrinksFor(player.id, shootsGame)
  })

  if (!shootsGame.currentGameMaster) {
    return <Background showCircles />
  }

  return <Background showCircles>
    <Grid
      container
      direction='column'
      justify='center'
      alignItems='center'
      style={{ height: '100%' }}
      >
      <Grid>
        <span role='img' aria-label='emoji' style={{ fontSize: 68 }}>🥴</span>
      </Grid>
      <Grid>
        <Typography variant='h6'>Game over!</Typography>
      </Grid>
      <Grid>
        <Typography style={{ textAlign: 'center' }}>
          Speel nog een keer met elkaar of ga terug naar het start scherm.
        </Typography>
      </Grid>
      <PlayersList
        players={sortPlayersOnWin(gameState.players, shootsGame)}
        // responses={responses}
        // currentQuestion={getCurrentQuestion}
        gameMaster={shootsGame.currentGameMaster}
        drinks={drinks}
        />
    </Grid>
    {
    isHost && <Footer
      buttons={{
            leftButton: {
                //  title: 'Hoofdmenu'
            },
            rightButton: {
                title: 'Rematch!',
                onClick: () => actions.rematch()
            }
        }}
    />
    }
  </Background>
}

GameEnd.propTypes = {
  history: PropTypes.object
}

export default GameEnd
