import React from 'react'
import PropTypes from 'prop-types'

import styled from 'styled-components'

const Holder = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: ${props => props.width + 'px' || `34px`};
  height: ${props => props.height + 'px' || `34px`};
  border-radius: ${props => props.borderRadius + 'px' || `4px`};
  background-color: ${props => props.color};
  overflow: hidden;
`

const COLORS = {
  FACEBOOK: '#4E71A8',
  INSTAGRAM: '#FA8D9C',
  SHARE: '#64BFC4',
  SHOOTS: '#9A76FF',
  FAQ: '#64BFC4',
  TERMS: '#F6CD6F',
  PP: '#7CBAF7'
}

const getColor = (type) => ({
  'facebook': COLORS.FACEBOOK,
  'instagram': COLORS.INSTAGRAM,
  'share': COLORS.SHARE,
  'shoots': COLORS.SHOOTS,
  'faq': COLORS.FAQ,
  'tos': COLORS.TERMS,
  'pp': COLORS.PP
})[type]

const Icon = ({ children, type, width, height, borderRadius }) => {
  const color = getColor(type)

  return (
    <Holder
      type={type}
      width={width}
      height={height}
      borderRadius={borderRadius}
      color={color}
      >
      {children}
    </Holder>
  )
}

Icon.defaultProps = {
  type: 'info',
  width: 32,
  height: 32,
  borderRadius: 10
}

Icon.propTypes = {
  children: PropTypes.any.isRequired,
  type: PropTypes.string.isRequired,
  width: PropTypes.number,
  height: PropTypes.number,
  borderRadius: PropTypes.number
}

export default Icon
