import React, { memo } from 'react'
import PropTypes from 'prop-types'

import Modal from '../modal'

import { withStyles } from '@material-ui/core/styles'
import { withRouter } from 'react-router'

import Switch from '@material-ui/core/Switch'
import { Icons } from '../../helpers/icons'

import Typography from '@material-ui/core/Typography'
import Grid from '@material-ui/core/Grid'

import Icon from './icon'
import { MD } from '../../helpers/mobile-detect'

import Text from './../../core/translations'
import Version from './../version'
import SelectLang from './select-language'

import { isDebug } from '../../helpers/shoots-helper'
import { useShootsContext } from '../../context/shoots-context'
import SocialUrl from '../../helpers/earls'

const styles = theme => ({
  container: {
    display: 'flex',
    justifyContent: 'flex-start',
    alignItems: 'center',
    borderTop: `1px solid ${theme.palette.border.light}`,
    paddingTop: 12,
    marginBottom: 12,
    width: '100%',

    '&:hover': {
      cursor: 'pointer'
    }
  },
  title: {
    fontSize: 16,
    marginBottom: 16,
    color: `${theme.palette.primary.black} !important`,
    fontWeight: '600',
    textAlign: 'left'
  },
  text: {
    color: `${theme.palette.alternate.text} !important`,
    fontWeight: '600',
    textAlign: 'left !important'
  },
  link: {
    textDecoration: 'underline'
  },
  svgHolder: {
    width: 32,
    height: 32,
    borderRadius: 8,
    overflow: 'hidden'
  },
  wrapper: {
    paddingBottom: theme.spacing(2)
  }
})

const SettingsDialog = ({ history, open, onClose, classes }) => {
  const { settings, setSettings } = useShootsContext()
  const DEBUG_MODE = isDebug()

  const handleChange = localStorageKey => event => {
    setSettings({ ...settings, [localStorageKey]: event.target.checked })
    console.log(`Settings set: ${localStorageKey}: ${event.target.checked}`)
  }

  const followSocial = (social) => {
    window.open(SocialUrl[social])
  }

  const shareShoots = () => {
    if (navigator.share) {
      navigator.share({
        title: 'Shoots',
        text: <Text text='settings.shareMessage' />,
        // text: 'Speel het vetste drink spel op je mobiel met je matties!',
        url: window.location.href
      }).then(() => {
        console.log('Thanks for sharing! 😄')
      })
        .catch(err => {
          console.log(`Couldn't share 🙁 because of`, err.message)
        })
    } else {
      console.log('web share not supported 🙅‍')
    }
  }

  const modalContent = {
    title: <Text text='settings.title' />,
    content: <>
      {/* <!--Settings --> */}
      <div className={classes.wrapper}>
        <Typography className={classes.title}><Text text='settings.general' /></Typography>
        <Grid container className={classes.container}>
          <Grid align='left' item xs={6}>
            <Typography variant='body1' className={classes.text} gutterBottom ><Text text='settings.music' /></Typography>
          </Grid>
          <Grid item xs={6} align='right'>
            <Switch
              checked={(settings && settings.music) || false}
              onChange={handleChange('music')}
              value='music'
              color='secondary'
            />
          </Grid>
          { (DEBUG_MODE) && <>
            <Grid align='left' item xs={6}>
              <Typography variant='body1' className={classes.text} gutterBottom ><Text text='settings.debug' /></Typography>
            </Grid>
            <Grid item xs={6} align='right'>
              <Switch
                checked={(settings && settings.debug) || false}
                onChange={handleChange('debug')}
                value='debug'
                color='secondary'
            />
            </Grid>
          </>
          }
        </Grid>

      </div>

      {/* <!--Social --> */}
      <div className={classes.wrapper}>
        <Typography className={classes.title}><Text text='settings.socialMedia' /></Typography>
        <Grid container onClick={() => followSocial('facebook')} className={classes.container} alignItems='center' justify='flex-start'>
          <Grid item xs={2}>
            <Icon type='facebook'>{Icons.facebook}</Icon>
          </Grid>

          <Grid item xs={10}>
            <Typography variant='body1' className={classes.text}><Text text='settings.facebook' /></Typography>
          </Grid>
        </Grid>
        <Grid onClick={() => followSocial('instagram')} container className={classes.container} alignItems='center' justify='flex-start'>
          <Grid item xs={2}>
            <Icon type='instagram'>{Icons.instagram}</Icon>
          </Grid>
          <Grid item xs={10}>
            <Typography variant='body1' className={classes.text}><Text text='settings.instagram' /></Typography>
          </Grid>
        </Grid>

        <Grid onClick={shareShoots} container className={classes.container} alignItems='center' justify='flex-start'>
          <Grid item xs={2}>
            <Icon type='share'>{Icons.share}</Icon>
          </Grid>
          <Grid item xs={10}>
            <Typography variant='body1' className={classes.text}><Text text='settings.share' /></Typography>
          </Grid>
        </Grid>
      </div>

      {/* <!--Settings --> */}
      <div className={classes.wrapper}>
        <Typography className={classes.title}><Text text='settings.info' /></Typography>

        <Grid container className={classes.container} alignItems='center' justify='flex-start'>
          <Grid item xs={2}>
            <Icon type='shoots'>{Icons.language}</Icon>
          </Grid>
          <Grid item align='left' xs={10}>
            <SelectLang textClass={classes.text} />
          </Grid>
        </Grid>

        <Grid container className={classes.container} alignItems='center' justify='flex-start'>
          <Grid item xs={2}>
            <Icon type='shoots'>{Icons.whatIsShoots}</Icon>
          </Grid>
          <Grid item align='left' xs={10}>
            <Typography variant='body1' className={classes.text}><Text text='settings.whatIsShoots' /></Typography>
          </Grid>
        </Grid>

        <Grid container className={classes.container} alignItems='center' justify='flex-start'>
          <Grid item xs={2}>
            <Icon type='faq'>{Icons.faq}</Icon>
          </Grid>
          <Grid item align='left' xs={10}>
            <Typography variant='body1' className={classes.text}><Text text='settings.faq' /></Typography>
          </Grid>
        </Grid>

        <Grid container className={classes.container} alignItems='center' justify='flex-start'>
          <Grid item xs={2}>
            <Icon type='tos'>{Icons.tos}</Icon>
          </Grid>

          <Grid item align='left' xs={10}>
            <Typography variant='body1' className={classes.text}><Text text='settings.generalConditions' /></Typography>
          </Grid>
        </Grid>

        <Grid container className={classes.container} alignItems='center' justify='flex-start'>
          <Grid item xs={2}>
            <Icon type='pp'>{Icons.privacyPolicy}</Icon>
          </Grid>

          <Grid item align='left' xs={10}>
            <Typography variant='body1' className={classes.text}><Text text='settings.privacy' /></Typography>
          </Grid>
        </Grid>
      </div>
      {
        DEBUG_MODE && <div className={classes.wrapper}><Version /></div>
      }
    </>,
    buttons: [
      {
        title: <Text text='settings.close' />,
        color: 'blue',
        onClick: onClose
      }
    ]
  }

  const width = MD() ? '100%' : '400px'
  const fullscreen = MD()

  return (
    <Modal isShowing={open} fullScreen={fullscreen} width={width} content={modalContent} onClose={onClose} customContent />
  )
}

SettingsDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  classes: PropTypes.object.isRequired,
  onClose: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired
}

export default memo(withRouter(withStyles(styles)(SettingsDialog)))
