import React, { memo, useState, useContext } from 'react'
import PropTypes from 'prop-types'

import MetaTags from 'react-meta-tags'

const ColorContext = React.createContext({})
const useColorMeta = () => useContext(ColorContext)

const ColorMetaTags = ({ children }) => {
  const [color, setColor] = useState('#e947da')

  return <ColorContext.Provider value={{ color, setColor }}>
    <MetaTags>
      {/* <!-- Chrome, Firefox OS and Opera --> */}
      <meta name='theme-color' content={color} />
      {/* <!-- Windows Phone --> */}
      <meta name='msapplication-navbutton-color' content={color} />
      {/* <!-- iOS Safari --> */}
      <meta name='apple-mobile-web-app-status-bar-style' content={color} />
    </MetaTags>
    {children}
  </ColorContext.Provider>
}

ColorMetaTags.propTypes = {
  children: PropTypes.node
}

export { useColorMeta }

export default memo(ColorMetaTags)
