import React, { memo, useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import { withRouter } from 'react-router'
import { makeStyles } from '@material-ui/core/styles'

import Modal from '../modal'
import useModal from '../../hooks/useModal'

import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Button from '@material-ui/core/Button'

import { useShootsContext } from '../../context/shoots-context'
import { isDebug } from '../../helpers/shoots-helper'
import Text from './../../core/translations'

const useStyles = makeStyles(theme => ({
  appBar: {
    top: 'auto',
    bottom: 0,
    backgroundColor: theme.palette.background.light
  },
  grow: {
    flexGrow: 1
  },
  startButton: {
    color: theme.palette.primary.main
  },
  cancelButton: {
    color: theme.palette.secondary.main
  }
}))

const Footer = ({ history, children, showModal, buttons: { leftButton, rightButton } }) => {
  const classes = useStyles()
  const DEBUG_MODE = isDebug()

  const { toggle, isShowing } = useModal()
  const [ isAlone, setAlone ] = useState(false)

  const { isHost, actions, setGameState, gameState } = useShootsContext()

  const startGame = () => {
    const settings = window.localStorage.getItem('game-settings')
    const gameSettings = JSON.parse(settings) || null

    actions.startGame(gameSettings)
  }

  const enableStartGameButton = () => {
    if ((gameState && gameState.players && gameState.players.length === 1) && !DEBUG_MODE) {
      setAlone(true)
    } else {
      setAlone(false)
    }
  }

  useEffect(() => {
    enableStartGameButton()
  }, [gameState])

  // REFACTOR
  // make something more generic for 'ja' button
  const modalContent = {
    title: <Text text='footer.leaveTitle' />,
    content: <Text text='footer.leaveQuestion' />,
    buttons: [
      {
        title: <Text text='footer.no' />,
        color: 'red',
        onClick: toggle
      },
      {
        title: <Text text='footer.yes' />,
        color: 'blue',
        onClick: () => {
          leaveGame()
          toggle()
        }
      }
    ]
  }

  const leaveGame = () => {
    actions.playerLeave()
    setGameState({})
    window.localStorage.removeItem('gamePin')
    history.push('/')
  }

  const handleCancelButton = () => {
    if (showModal) {
      toggle()
    } else {
      history.push('/')
    }
  }

  return (
    <React.Fragment>
      <AppBar position='fixed' color='secondary' className={classes.appBar}>
        <Toolbar>
          {
          children ||
          (
            <React.Fragment>
              {
                leftButton && (
                <Button className={classes.cancelButton} onClick={handleCancelButton}>
                  <Text text={leftButton.title} />
                </Button>
              )}
              <div className={classes.grow} />
              {
                rightButton && (isHost || DEBUG_MODE) && (
                <Button onClick={rightButton.onClick || startGame} className={classes.startButton} color='inherit' disabled={isAlone}>
                  <Text text={rightButton.title} />
                </Button>
            )}
            </React.Fragment>
          )
        }
        </Toolbar>
      </AppBar>
      <Modal onClose={toggle} isShowing={isShowing} content={modalContent} />
    </React.Fragment>
  )
}

Footer.defaultProps = {
  showModal: false,
  buttons: {
    leftButton: {
      title: 'footer.cancel'
    },
    rightButton: {
      title: 'footer.start'
    }
  }
}

Footer.propTypes = {
  history: PropTypes.object.isRequired,
  children: PropTypes.any,
  buttons: PropTypes.object,
  showModal: PropTypes.bool
}

export default withRouter(memo(Footer))
