import React, { memo } from 'react'
import PropTypes from 'prop-types'

import useAudio from '../../hooks/useAudio'
import styled from 'styled-components'

import IconButton from '@material-ui/core/IconButton'
import Icon from '@material-ui/core/Icon'

const StyledIcon = styled(Icon)`
  color: ${({ color, theme }) => color || theme.palette.primary.light};
  font-size: ${({ fontSize }) =>
    fontSize
      ? fontSize === 'large'
        ? '34px !important'
        : '24px !important'
      : 'initial'
};
`

const VolumeControl = ({ url, color, fontSize }) => {
  const { playing, toggle } = useAudio(url)

  return (
    <IconButton onClick={toggle}>
      <StyledIcon color={color} fontSize={fontSize}>
        {playing ? 'volume_up' : 'volume_mute'}
      </StyledIcon>
    </IconButton>
  )
}

VolumeControl.defaultProps = {
  url: '/media/shoots.mp3',
  fontSize: 'large'
}

VolumeControl.propTypes = {
  url: PropTypes.string,
  color: PropTypes.string,
  fontSize: PropTypes.string
}

export default memo(VolumeControl)
